.cart-min-item {
  display: flex;
  font-size: 14px;
  line-height: 19px;
  color: inherit;

  &__left {
    width: 110px;
  }

  &__right {
    width: calc(100% - 110px);
    padding-left: 30px;
    display: flex;
    flex-direction: column;

    @include sm-block {
      padding-left: 20px;
    }
  }

  &__img {
    position: relative;
    width: 100%;
    @include ratio(1, 1);

    img {
      @include coverdiv();
      @include ofi(cover, center);
    }
  }

  &__count {
    margin-top: 12px;
  }

  &__price-box {
    margin-top: auto;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    color: $accent;
  }

  &__price {
    font-size: 24px;
  }

  &__art {
    margin-bottom: 8px;
  }

  &__title {
    a {
      color: inherit;
      transition: color .3s ease;

      &:hover {
        color: $accent;
      }
    }
  }

  &__del {
    top: 7px;
    right: 20px;
  }
}
