.mobile-menu {
  z-index: z-index(mobile-menu);
  background-color: $darkbBg;
  color: #bfc3c8;
  padding: 90px 65px 70px;
  transform: translateX(-100%);
  display: none;

  &.active {
    transform: translateX(0);
  }

  @include sm-block {
    display: block;
  }

  &__list {
    margin: 0 -65px;

    li {
      border-top: 1px solid rgba(#fff, .1);

      &:last-child {
        border-bottom: 1px solid rgba(#fff, .1);
      }
    }

    a {
      display: block;
      padding: 7px 65px;
      color: inherit;
      position: relative;

      &:after {
        content: '';
        display: block;
        width: 4px;
        height: 8px;
        background-image: url('#{$pathImg}/chevron-right.svg');
        background-size: cover;
        position: absolute;
        top: 50%;
        right: 65px;
        z-index: 2;
        opacity: .5;
        transform: translate(0, -50%);
      }

      &.active,
      &:hover {
        color: #fff;

        &:after {
          opacity: 1;
        }
      }
    }
  }

  &__contacts {
    margin-top: 30px;

    li {
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      color: inherit;
    }
  }

  &__recall {
    position: absolute;
    top: 25px;
    left: 65px;
    color: #fff;
    font-size: 13px;
    font-weight: 500;

    &:after {
      content: '';
      display: block;
      height: 1px;
      width: 100%;
      background-color: #fff;
      opacity: .5;
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}
