.header {
  box-shadow: 0px 3px 40px 0px rgba(0, 0, 0, 0.05);

  &__container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;

    @include lg-block {
      padding: 0 20px;
    }

    @include sm-block {
      padding: 7px 20px 12px;
    }
  }

  &__logo {
    color: $accent;
    width: 210px;

    @include md-block {
      width: 150px;
      margin-top: -20px;
    }

    @include sm-block {
      margin-top: 0;
    }

    a {
      color: inherit;
    }

    .btn {
      color: #fff;

      @include md-block {
        margin-top: 20px;
      }
    }

    .icon {
      font-size: 94px;

      @include lg-block {
        font-size: 78px;
      }

      @include md-block {
        font-size: 66px;
      }

      @include sm-block {
        font-size: 50px;
      }
    }
  }

  &__content {
    width: calc(100% - 280px);

    @include lg-block {
      width: calc(100% - 215px);
    }

    @include md-block {
      width: calc(100% - 165px);
    }

    @include sm-block {
      display: none;
    }
  }

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 70px;
    border-bottom: 1px solid #f0f0f1;
  }

  &__contacts {
    display: flex;
    align-items: center;
    font-size: 13px;

    li {
      margin-right: 30px;

      @include lg-block {
        margin-right: 20px;
      }

      &:last-child {
        margin-right: 0;
      }
    }

    a {
      color: #727273;
    }
  }

  &__menu {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    height: 88px;
  }

  &__nav {
    width: calc(100% - 320px);

    @include lg-block {
      width: calc(100% - 280px);
    }

    @include md-block {
      width: calc(100% - 150px);
    }
  }

  &__nav-list {
    display: flex;
    justify-content: space-between;

    li {
      //margin-right: 60px;

      &:last-child {
        margin-right: 0;
      }
    }

    a {
      color: #1b2128;
      font-weight: 500;
      transition: color .2s ease;
      display: inline-block;
      position: relative;
      padding: 5px 0;

      &:after {
        content: '';
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        height: 3px;
        background-color: rgba($accent, .5);
        transform: scaleX(0);
        transform-origin: left bottom;
        transition: transform .2s ease-in-out;
      }

      &:hover {
        // color: $accent;

        &:after {
          transform: scaleX(1);
        }
      }
    }
  }

  &__search {
    width: 215px;

    @include md-block {
      width: 115px;
    }
  }

  &__links {
    display: flex;
    align-items: center;

    li {
      margin-right: 40px;

      @include lg-block {
        margin-right: 20px;
      }

      &:last-child {
        margin-right: 0;
      }
    }
  }

  &__hamb {
    display: none;

    @include sm-block {
      display: block;
    }
  }

  &__btn {
    height: 34px;
    line-height: 34px;
    width: 145px;
    font-size: 13px;
    font-weight: 500;
  }
}
