.price-table {
  width: 100%;
  max-width: 680px;

  tr {
    &:last-child {
      td {
        padding-bottom: 0;
      }
    }
  }

  td {
    padding-bottom: 10px;

    &:last-child {
      text-align: right;
    }
  }

  &__total {
    font-size: 24px;
    color: $accent;
  }
}
