.element-info {
  margin-bottom: 50px;
  color: #1b2128;
  font-size: 14px;
  line-height: 19px;

  @include md-block {
    margin-bottom: 20px;
  }

  .modal-fast & {
    margin-bottom: 0;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
  }

  &__col {
    padding: 0 15px;
    width: 33.33%;

    @include md-block {
      width: 50%;
      margin-bottom: 30px;
    }

    @include sm-block {
      width: 100%;
    }
  }

  .modal-fast &__col {
    width: 50%;
    margin-bottom: 30px;

    @include sm-block {
      width: 100%;
    }
  }

  &__title {
    border-radius: 5px;
    background-color: #f1f4fa;
    text-align: center;
    height: 38px;
    line-height: 38px;
    margin-bottom: 25px;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 600;
  }

  &__review-empty {
    color: $accent;
    font-weight: 600;
  }

  &__review-btn {
    width: 170px;
    height: 30px;
    line-height: 30px;
    margin-top: 30px;
    background-color: #28303b;
  }
}
