.catalog-top {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-bottom: 30px;

  @include sm-block {
    margin-bottom: 20px;
    display: block;
  }

  &__show {
    @include sm-block {
      margin-bottom: 7px;
      display: block;
    }
  }

  &__count {
    color: #727273;

    @include sm-block {
      display: none;
    }

    a {
      color: inherit;
      transition: color .3s ease;
      display: inline-block;
      margin-left: 10px;

      &:hover,
      &.active {
        color: #1b2128;
      }
    }
  }
}
