.input-line {
  display: flex;
  //flex-wrap: wrap;
  //align-items: center;
  max-width: 550px;
  font-weight: normal;
  position: relative;

  @include sm-block {
    display: block;
  }

  .input {
    max-width: none;

    &:read-only {
      border: none;
      padding-left: 0;
      padding-right: 0;
    }
  }

  &__label {
    display: block;
    width: 185px;
    margin-right: 30px;
    padding-top: 8px;

    @include sm-block {
      width: 100%;
      margin-bottom: 10px;
    }
  }

  &__input {
    flex-grow: 1;
  }

  &__edit {
    margin-left: 30px;
    width: 40px;

    @include sm-block {
      position: absolute;
      top: -5px;
      right: 0;
    }
  }
}
