.inline-center {
	text-align: center;
}

.nowrap {
	white-space: nowrap;
}

.mt15 {
	margin-top: 15px;
}

.xs-mb20 {
	@include xs-block {
		margin-bottom: 20px;
	}
}

.hide {
	display: none !important;
}

.sm-hide {
	@include sm-block {
		display: none !important;
	}
}

.md-hide {
	@include md-block {
		display: none !important;
	}
}

.md-show {
	display: none !important;

	@include md-block {
		display: block !important;
	}
}

.sm-show-flex {
	display: none !important;

	@include sm-block {
		display: flex !important;
	}
}

.lg-hide {
	@include lg-block {
		display: none !important;
	}
}

.lg-show {
	display: none !important;

	@include lg-block {
		display: block !important;
	}
}

.lg-show-flex {
	display: none !important;

	@include lg-block {
		display: flex !important;
	}
}
