.price {
  white-space: nowrap;
  position: relative;

  .icon {
    font-size: .75em;
  }

  &__old {
    position: absolute;
    //bottom: calc(100% + 1px);
    bottom: 100%;
    left: 0;
    font-size: 12px;
    color: $grey-1;

    &:before {
      content: '';
      display: block;
      height: 1px;
      width: 100%;
      background-color: currentColor;
      position: absolute;
      top: 50%;
      left: 0;
    }
  }

  .element-options__price-value &__old {
    bottom: calc(100% - 5px);
  }
}
