.slider-range__container {
  position: relative;
  margin: 10px 0;
}

.slider-range__range-bg {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #f0f0f1;
  height: 2px;
  width: 100%;
}

.ui-slider {
  position: relative;
  height: 2px;
  width: calc(100% - 14px);
}

.ui-widget-header {
  //background-color: rgba(#bea78b, .3);
  position: absolute;
  height: 2px;
  top: 0;
}

.ui-slider-handle {
  position: absolute;
  z-index: 2;
  top: -7px;
  width: 14px;
  height: 14px;
  background-color: rgba(#8d929b, 1);
  cursor: pointer;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;

  &:after {
    content: '';
    display: block;
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: #fff;
  }
}

.handle-text {
  position: absolute;
  top: calc(100% + 10px);
  font-size: 14px;
}

.slider-range {
  &__head {
    margin-bottom: 25px;
    display: flex;
    justify-content: space-between;
  }

  &__head-block {
    display: flex;
    margin-right: 15px;
  }

  &__input {
    width: 50px;
    border: 1px solid #4d3c29;
    border-width: 0 0 1px 0;
    text-align: center;
    font-size: 14px;
    color: #604d37;
    font-weight: bold;
  }
}
