.social {
  display: flex;
  flex-wrap: wrap;

  li {
    margin-right: 20px;
  }

  a {
    color: inherit;
    opacity: .5;
    transition: opacity .2s ease;
    font-size: 0;

    &:hover {
      opacity: 1;
    }

    .icon {
      font-size: 13px;
    }

    .icon-vk {
      font-size: 13px;
    }
  }
}
