.catalog-list {
  @include row();
  position: relative;

  @include md-block {
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
  }

  @include xs-block {
    margin-left: -15px;
    margin-right: -15px;
  }

  &__item {
    @include col();
    width: 25%;
    margin-bottom: 30px;

    @include lg-block {
      width: 33.33%;
    }

    @include md-block {
      width: 50%;
    }

    @include xs-block {
      width: 100%;
      padding: 0;
      max-width: 270px;
      margin-left: auto;
      margin-right: auto;
    }

    &:nth-child(4) {
      @include lg-block {
        display: none;
      }

      @include md-block {
        display: block;
      }
    }

    .catalog-item {
      height: 100%;
      display: flex;
      flex-direction: column;
    }

    .catalog-item__name {
      margin-bottom: auto;
    }

    .catalog-item__content {
      flex-grow: 1;
      display: flex;
      flex-direction: column;
    }
  }
}
