.cadesign {
  color: #fff;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  font-size: 0;
  opacity: .5;
  transition: opacity .2s ease;

  &:hover {
    opacity: 1;
  }

  &__title {
    font-size: 14px;

    @include md-block {
      font-size: 12px;
    }

    @include sm-block {
      display: none;
    }
  }

  .icon {
    font-size: 18px;
    margin-left: 3px;
  }
}
