.hamb {
  font-size: 38px;
  width: 1em;
  height: 1em;
  background-color: $accent;
  color: #fff;
  z-index: 0;
  position: relative;
  border-radius: 50%;

  &:before,
  &:after {
    content: '';
    display: block;
    width: .45em;
    height: 2px;
    opacity: .8;
    position: absolute;
    top: 50%;
    left: 50%;
    background-color: currentColor;
    transform: translate(-50%, -50%);
    transform-origin: center center;
    z-index: 2;
    transition: transform .2s ease-in-out;
  }

  &:before {
    margin-top: -3px;
  }

  &:after {
    margin-top: 3px;
  }

  &.active {
    &:before {
      //margin-top: 0;
      transform: translate(-50%, calc(-50%  + 3px)) rotate(45deg);
    }
    &:after {
      //margin-top: 0;
      transform: translate(-50%, calc(-50% - 3px)) rotate(-45deg);
    }
  }
}
