* {
  box-sizing: border-box;
}

.counter {
  margin: 0;
  padding: 0;
  list-style: none;
  font-size: 0;
  width: 110px;
  height: 30px;
  overflow: hidden;
  display: flex;
  border: 1px solid #eeeeef;

  li {
    display: flex;
    align-items: center;
    justify-content: center;
    vertical-align: top;
    text-align: center;
  }

  &__btn {
    font-size: 14px;
    white-space: nowrap;
    cursor: pointer;
    background-color: #fff;
    width: 25px;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100%;
    color: #1b2128;
    user-select: none;
    transition: background-color .3s ease;

    &:hover {
      background-color: #eeeeef;
    }
  }

  input {
    width: 58px;
    height: 100%;
    //line-height: 30px;
    background-color: #fff;
    border: 1px solid #eeeeef;
    border-width: 0 1px;
    outline: none;
    font-size: 14px;
    font-weight: 400;
    text-align: center;
    color: #1b2128;

    &.error {
      color: $error;
    }
  }
}
