.top-page-title {
  height: 110px;
  background-size: cover;
  background-position: center;
  position: relative;
  font-size: 30px;
  color: #fff;

  @include sm-block {
    font-size: 24px;
    height: 60px;
  }

  &:after {
    content: '';
    @include coverdiv();
    background-color: rgba(#000, .3);
  }

  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    z-index: 2;
    position: relative;
  }

  &__desc {
    background-color: $accent;
    color: #fff;
    font-size: 14px;
    padding: 7px 30px;
    border-radius: 5px;
    position: absolute;
    right: 50px;
    bottom: 20px;

    @include md-block {
      padding: 7px 8px;
      font-weight: 400;
      right: 20px;
    }

    @include sm-block {
      width: 104px;
      font-size: 13px;
      right: 0;
      bottom: auto;
      border-radius: 3px;
      top: 50%;
      transform: translateY(-50%);
    }
  }
}
