.cart-sidebar {
  border: 1px solid #e9e9ea;
  position: relative;

  $p: &;

  @include lg-block {
    display: flex;
    font-size: 16px;
    //flex-wrap: wrap;
  }

  @include sm-block {
    display: block;
    font-size: 14px;
  }

  & + & {
    margin-top: 40px;

    @include lg-block {
      margin-top: 30px;
    }
  }

  &:after {
    content: '';
    display: block;
    height: 12px;
    width: 100%;
    position: absolute;
    z-index: 5;
    bottom: 0;
    left: 0;
    background-image: url('../img/pattern-green.png');
    background-position: 50%;
    opacity: .3;

    @include lg-block {
      display: none;
    }

    @include sm-block {
      display: block;
    }
  }

  &--grey {
    padding: 30px;
    background-color: #f1f4fa;

    &:after {
      display: none;

      @include sm-block {
        display: none;
      }
    }

    @include lg-block {
      padding: 30px;
    }

    @include sm-block {
      padding: 30px;
    }
  }

  &__head {
    @include lg-block {
      padding-right: 30px;
      width: 320px;
    }

    @include md-block {
      width: 240px;
    }

    @include sm-block {
      width: auto;
    }
  }

  &__body {
    @include lg-block {
      flex-grow: 1;
      max-width: 270px;
    }

    @include sm-block {
      max-width: none;
    }
  }

  &__icon {
    font-size: 40px;
    font-weight: 600;
    line-height: .8;
    position: absolute;
    z-index: 2;
    top: 10px;
    right: 8px;
    color: $accent;
  }

  &__inner {
    padding: 30px;

    @include lg-block {
      padding: 30px 30px 25px;
    }

    @include sm-block {
      padding: 30px;
    }
  }

  &__white {
    background-color: #fff;

    @include lg-block {
      flex-grow: 1;
      display: flex;
    }

    @include sm-block {
      display: block;
    }
  }

  &__grey {
    background-color: #f1f4fa;

    @include lg-block {
      flex-grow: 1;
      max-width: 320px;
      position: relative;
      border-left: 1px solid #e9e9ea;
    }

    @include md-block {
      max-width: 240px;
    }

    @include sm-block {
      max-width: none;
      padding: 25px 30px 33px;
    }

    &:after {
      content: '';
      display: none;
      height: 12px;
      width: 100%;
      position: absolute;
      z-index: 5;
      bottom: 0;
      left: 0;
      background-image: url('../img/pattern-green.png');
      background-position: 50%;
      opacity: .3;

      @include lg-block {
        display: block;
      }

      @include sm-block {
        display: none;
      }
    }
  }

  &__title  {
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 20px;
  }

  &__desc {
    font-weight: 600;
    margin-bottom: 20px;
    color: $accent;
  }

  &__p {
    margin-bottom: 10px;

    &:last-child {
      margin-bottom: 0;
    }

    #{$p}__link {
      margin-top: 10px;

      @include lg-block {
        margin-top: 0;
      }
    }
  }

  &__total {
    margin-top: 15px;
    font-size: 20px;

    .price {
      font-size: 24px;
      color: $accent;
    }
  }

  &__link {
    display: inline-block;
    color: inherit;
    font-weight: bold;
    transition: color .3s ease;

    &:hover {
      color: $accent;
    }
  }

  &__sales-table {
    margin-top: 15px;
    max-width: 280px;
  }

  &__box {
    flex-grow: 1;
  }
}
