.link-icon {
  display: flex;
  align-items: center;
  color: $grey-1;
  position: relative;
  transition: color .2s ease-in-out;
  cursor: pointer;

  &:hover {
    color: $accent;
  }

  &__icon {
    width: 38px;
    height: 38px;
    font-size: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #eff3fa;
    border-radius: 50%;
    flex-shrink: 0;
    transition: all .2s ease-in-out;

    .icon {
      font-size: 15px;
      opacity: .7;
    }

    .icon-mail {
      font-size: 15px;
    }

    .icon-phone {
      font-size: 19px;
    }

    .icon-cart {
      font-size: 19px;
    }

    .icon-user {
      font-size: 17px;
    }
  }

  &--md-hide-icon &__icon {
    @include md-block {
      display: none;
    }
  }

  &:hover &__icon {
    background-color: $accent;
    color: #fff;
  }

  &__text {
    display: inline-block;
    margin-left: 10px;
  }

  &--md-hide-icon &__text {
    @include md-block {
      margin-left: 0;
    }
  }

  &__count {
    display: block;
    width: 17px;
    height: 17px;
    border-radius: 4px;
    line-height: 17px;
    font-size: 10px;
    background-color: $accent2;
    color: #fff;
    font-weight: 700;
    text-align: center;
    position: absolute;
    top: 0;
    right: 5px;
  }
}