.catalog-filter {
  margin-bottom: 40px;

  &__top {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 20px;
  }

  &__title {
    color: #1b2128;
    text-transform: uppercase;
    font-weight: 600;
    font-size: 18px;
  }

  &__btns {
    display: flex;

    & > *:not(:first-child) {
      margin-left: 30px;
    }

    @include sm-block {
      margin: 0 -10px;
    }
  }

  &__btn {
    width: 170px;
    height: 30px;
    line-height: 30px;
    background-color: #28303b;
    font-size: 14px;

    @include sm-block {
      width: calc(50% - 20px);
      margin: 0 10px;
    }

    &--reset {
      background-color: #5b5e62;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;

    @include sm-block {
      margin: 0 -10px;
    }
  }

  &__item {
    padding: 0 15px;
    width: p(1, 6);
    //margin-bottom: 20px;

    @include lg-block {
      width: p(1, 4);
    }

    @include sm-block {
      width: 50%;
      padding: 0 10px;
    }
  }
}
