.footer {
  background-color: $darkbBg;
  color: #fff;

  @include sm-block {
    margin-bottom: 46px;
  }

  &:after {
    content: '';
    display: block;
    height: 11px;
    background-image: url('#{$pathImg}/pattern.png');
    background-repeat: repeat;
    background-size: 30px;
    background-position: center;
  }

  &__container {
    @include lg-block {
      padding: 0 20px;
    }
  }

  &__top {
    padding: 35px 0;

    @include md-block {
      padding: 35px 0 0;
    }

    @include sm-block {
      padding: 20px 0 15px;
    }
  }

  &__top-row {
    display: flex;
    flex-wrap: wrap;
  }

  &__menu {
    width: 80%;
    display: flex;
    flex-wrap: wrap;

    @include md-block {
      width: 100%;
      margin-bottom: 30px;
    }

    @include sm-block {
      display: block;
      margin-bottom: 0;
    }
  }

  &__menu-block {
    width: p(1, 4);
    padding-right: 30px;

    @include sm-block {
      width: 100%;
      padding-right: 0;
    }

    &--columns {
      width: p(2, 4);
    }

    &:last-child {
      @include md-block {
        padding-right: 0;
      }
    }
  }

  &__contacts {
    width: 20%;

    @include md-block {
      width: 100%;
      //display: flex;
    }
  }

  &__contacts-block {
    font-size: 14px;
    padding: 0 0 0 40px;

    @include md-block {
      padding: 0;
    }

    .footer-menu__list {
      position: relative;

      @include md-block {
        column-count: 2;
        width: 430px;
        margin-top: -8px;
      }

      @include sm-block {
        column-count: 1;
        width: calc(100% - 40px);
      }

      li {
        margin: 15px 0;

        @include md-block {
          margin: 8px 0 8px 40px;
          page-break-inside: avoid;
          break-inside: avoid-column;
          display: inline-block;
          width: 100%;
        }

        @include sm-block {
          margin: 10px 0 10px 40px;
        }
      }

      .footer__social {
        margin: 15px 0 15px -40px;

        @include md-block {
          position: absolute;
          top: 0;
          right: -80px;
          width: auto;
          margin: 0;
        }

        @include sm-block {
          margin: 10px 0 10px -40px;
          position: static;
        }
      }
    }
  }

  &__bottom {
    padding: 25px 0;
    border-top: 1px solid rgba(#fff, .2);
    font-size: 14px;

    @include md-block {
      font-size: 12px;
    }
  }

  &__bottom-row {
    display: flex;
    flex-wrap: wrap;
  }

  &__policy {
    margin-right: 85px;

    @include md-block {
      margin-right: 30px;
    }

    @include sm-block {
      width: 100%;
      margin: 0 0 20px;
    }

    a {
      color: inherit;
      opacity: .5;
      text-decoration: underline;
      transition: opacity .2s ease;

      &:hover {
        opacity: 1;
        text-decoration: none;
      }
    }
  }

  &__copy {
    opacity: .5;
    margin-right: auto;
  }
}
