.search-block {
  position: relative;
  font-size: 0;

  &__input {
    border: 1px solid #d3d4d6;
    border-width: 0 0 1px;
    height: 35px;
    color: #1b2128;
    font-weight: 500;
    padding-left: 40px;
    width: 100%;
    font-size: 16px;

    @include md-block {
      font-size: 14px;
    }

    &::placeholder {
      opacity: .5;
    }
  }

  .icon {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 16px;
    opacity: .5;
  }
}