.order-data {
  display: flex;
  font-weight: 400;

  @include sm-block {
    display: block;
  }

  &__title {
    width: 215px;
    padding-right: 20px;

    @include sm-block {
      width: 100%;
      margin: 0 0 20px;
      padding: 0;
    }
  }

  &__info {
    width: calc(100% - 215px);

    @include sm-block {
      width: 100%;
    }
  }

  &__row {
    display: flex;
    margin-bottom: 10px;

    @include sm-block {
      display: block;
      margin-bottom: 20px;
    }
  }

  &__row-title {
    width: 50%;

    @include sm-block {
      margin-bottom: 10px;
      width: 100%;
    }
  }

  &__row-value {
    width: 50%;

    @include sm-block {
      width: 100%;
    }
  }
}
