.sort {
  color: #727273;
  position: relative;
  white-space: nowrap;
  user-select: none;

  &.active {
    z-index: 10;
  }

  &__title {
    color: #1b2128;
    display: inline-block;
    white-space: nowrap;
    position: relative;
    padding-right: 15px;
    cursor: pointer;

    .icon {
      position: absolute;
      font-size: 5px;
      top: 9px;
      right: 0;
      margin-left: 5px;
      transition: transform .2s ease-in-out;
    }
  }

  &.active &__title {
    .icon {
      transform: rotate(180deg);
    }
  }

  &__body {
    position: absolute;
    background-color: #fff;
    top: 100%;
    left: 0;
    width: 100%;
    padding: 5px;
    box-shadow: 0 3px 40px 0 rgba(0,0,0,.05);
    opacity: 0;
    visibility: hidden;
    transition: all .2s ease-in-out;

    li {
      padding: 3px 10px;
      cursor: pointer;
    }
  }

  &.active &__body {
    opacity: 1;
    visibility: visible;
  }
}
