.float-btn {
  position: absolute;
  z-index: z-index(float-btn);
  top: 40vh;
  transform: translateY(-50%);
  right: 20px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;

  @include md-block {
    display: none;
  }

  li {
    margin-bottom: 20px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__item {
    overflow: hidden;
    width: 50px;
    height: 50px;
    border-radius: 25px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    background-color: #dde1e7;
    color: #828891;
    position: relative;
    transform: translate3d(0, 0, 0);
    transition: all .3s ease;

    &:hover {
      width: 175px;
      background-color: #aaafb7;
      color: #fff;
      transform: translate3d(45px, 0, 0);
    }
  }

  &__icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;

    .icon {
      font-size: 23px;
    }
  }

  &__text {
    flex-shrink: 0;
    font-size: 13px;
    line-height: 16px;
    opacity: 0;
    transition: opacity 0s ease-in-out 0s;
  }

  &__item:hover &__text {
    opacity: 1;
    transition: opacity .3s ease-in-out .2s;
  }
}
