.sizes-content {
  &__title {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 40px;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;

    @include xs-block {
      display: block;
    }
  }

  &__left {
    width: 210px;
    padding-right: 30px;

    @include sm-block {
      width: 150px;
    }

    @include xs-block {
      width: 100%;
    }
  }

  &__right {
    width: calc(100% - 210px);
    max-width: 490px;

    @include sm-block {
      width: calc(100% - 150px);
    }

    @include xs-block {
      width: 100%;
      max-width: none;
    }
  }

  &__heading {
    font-size: 24px;
    font-weight: 500;
    margin-bottom: 10px;
  }

  p {
    margin: .7em 0;
  }
}
