.category-item {
  background-color: #f1f4fa;
  display: block;
  color: inherit;
  height: 100%;
  transition: transform .3s ease;

  @include xs-block {
    display: flex;
    height: 120px;
  }

  &:hover {
    transform: scale(1.1);
  }

  &__img {
    @include ratio(210, 150);
    position: relative;
    font-size: 0;

    @include xs-block {
      width: 140px;
      flex-shrink: 0;
    }

    img {
      @include coverdiv();
      @include ofi(cover, top center);
    }

    .icon {
      font-size: 45px;
      color: #fff;
      position: absolute;
      left: 24px;
      bottom: 35px;

      @include xs-block {
        bottom: auto;
        top: 15px;
        left: 15px;
      }
    }
  }

  &__content {
    padding: 20px 30px 25px;

    @include xs-block {
      padding: 10px 20px;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }

  &__title {

  }

  &__link {
    display: inline-block;
    margin-top: 10px;
    font-size: 13px;
    font-weight: 300;
    color: #28303b;
  }
}
