.slider-counts {
  color: inherit;
  display: flex;
  font-weight: 600;
  line-height: .8;
  pointer-events: none;

  &__num {
    font-size: 80px;

    @include md-block {
      height: 60px;
    }
  }

  &__all {
    font-size: 30px;
    margin-left: 10px;

    @include md-block {
      font-size: 24px;
    }
  }
}
