.lock {
  overflow: hidden;
  height: 100%;
  width: 100%;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;

  body:not(.ready) & {
    display: none;
  }
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  padding: 20px;
  @include z-index(modal-overlay);
  transform: translateZ(0);
  opacity: 0;
  background-color: rgba(#1a1f27, .98);
  visibility: hidden;
  transition: all .3s ease-in-out;

  &.active {
    opacity: 1;
    visibility: visible;
    //z-index: 320;
  }

  body:not(.ready) & {
    display: none;
  }

  &__errors {
    color: $error;
    font-size: 12px;
    margin: -20px 0 20px;

    &:empty {
      display: none;
    }
  }

  &__inner {
    width: 100%;
    max-width: 550px;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    padding-left: 60px;
    margin: auto;
    z-index: 10;
    //overflow: hidden;

    @include sm-block {
      padding: 60px 0 0;
    }
  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 0;
    color: #1b2128;
    transition: color .3s ease;
    cursor: pointer;
    z-index: 3;

    @include sm-block {
      color: #fff;
      opacity: .5;
      transition: all .3s ease;
    }

    &:hover {
      color: $accent;

      @include sm-block {
        color: #fff;
        opacity: 1;
      }
    }

    .icon {
      font-size: 22px;
    }
  }

  &__decor {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 60px;
    background-color: $accent;
    color: #fff;
    text-align: center;
    font-size: 0;
    padding: 15px 0 0;
    border-radius: 5px 0 0 5px;

    @include sm-block {
      height: 60px;
      width: 100%;
      border-radius: 5px 5px 0 0;
      text-align: left;
      padding: 20px 30px 0;
    }

    &:after {
      content: '';
      display: block;
      height: 13px;
      width: 100%;
      background-image: url('../img/pattern.png');
      background-size: 30px;
      background-position: center;
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 1;

      @include sm-block {
        width: 13px;
        height: 100%;
        top: 0;
        left: 0;
      }
    }

    .icon {
      font-size: 22px;
      opacity: .3;
    }
  }

  &__content {
    padding: 45px 60px;

    @include sm-block {
      padding: 45px 35px;
    }
  }

  &__title {
    color: #42746e;
    font-size: 36px;
    margin-bottom: 40px;

    @include sm-block {
      font-size: 24px;
      margin-bottom: 30px;
    }
  }

  &__desc {
    margin-bottom: 40px;

    @include sm-block {
      margin-bottom: 30px;
    }
  }
}

.modal-info {
  &__inner {
    width: 100%;
    max-width: 1090px;
    background-color: #fff;
    border-radius: 5px;
    position: relative;
    z-index: 10;
    margin: auto;
    padding: 70px 80px;
    //overflow: hidden;

    @include md-block {
      padding: 50px 40px;
    }

    @include xs-block {
      width: calc(100% + 40px);
      margin: auto -20px;
      padding: 50px 20px;
    }

  }

  &__close {
    position: absolute;
    top: 20px;
    right: 20px;
    font-size: 0;
    color: #727273;
    transition: color .3s ease;
    cursor: pointer;
    z-index: 3;

    &:hover {
      color: $accent;
    }

    .icon {
      font-size: 22px;
    }
  }

  &__table-item {
    margin-bottom: 65px;

    &:last-child {
      margin-bottom: 0;
    }
  }
}

.modal-fast {
  &__inner {
    max-width: 775px;
    height: auto;
    padding: 60px 30px 30px;

    @include sm-block {
      padding: 60px 20px 20px;
      overflow-x: hidden;
      overflow-y: auto;
    }
  }
}

.modal-mobile {
  display: none;
  padding: 0;

  @include sm-block {
    display: block;
  }

  &__close {
    position: absolute;
    top: 27px;
    right: 30px;
    color: #828891;

    &:before {
      content: '';
      display: block;
      top: -10px;
      bottom: -10px;
      left: -10px;
      right: -10px;
      position: absolute;
    }

    .icon {
      font-size: 13px;
    }
  }

  &__inner {
    height: 100%;
    width: 100%;
    background-color: #fff;
    padding: 40px 20px 50px;

    &--search {
      padding: 18px 20px 50px;
    }
  }

  &__search {
    width: calc(100% - 64px);
    margin-bottom: 50px;
  }

  &__search-block {
    height: calc(100% - 110px);
  }
}
