.main-slider-images {
  position: relative;
  height: 100%;
  color: #fff;

  &__slider {
    height: 100%;
    position: relative;

    & .slick-list,
    & .slick-track,
    & .slick-slide > div {
      height: 100%;
    }

    .slick-dots {
      position: absolute;
      bottom: 40px;
      right: 100px;
      display: flex;

      @include md-block {
        right: 23px;
        bottom: 26px;
      }

      li {
        margin-left: 5px;
      }

      button {
        font-size: 0;
        height: 2px;
        width: 35px;
        background-color: #fff;
        border: none;
        transform-origin: left bottom;
        opacity: .5;
        transition: all .3s ease;
        cursor: pointer;

        @include xs-block {
          width: 20px;
        }
      }

      .slick-active {
        button {
          opacity: 1;
          transform: scaleY(2);
        }
      }
    }
  }

  &__slider:not(.slick-initialized) &__item {
    display: none;

    &:first-child {
      display: block;
    }
  }

  &__item {
    height: 100%;
  }

  &__img-box {
    height: 100%;
    width: 100%;
    position: relative;

    img {
      @include coverdiv();
      @include ofi(cover, center);
      display: block;
    }
  }

  &__counts {
    position: absolute;
    bottom: 30px;
    left: 90px;
    //margin-left: -590px;
    z-index: 5;

    @include lg-block {
      left: 20px;
    }
  }
}
