.order-table {
  border-radius: 5px;
  background-color: #fff;
  border: 1px solid #e9e9eb;
  overflow: hidden;
  font-size: 14px;
  
  &--no-border {
    border-radius: 0;
    border: none;
  }

  .price {
    font-weight: bold;
  }

  &__table {
    width: 100%;

    @include sm-block {
      display: block;
    }

    tbody {
      @include sm-block {
        display: block;
      }
    }

    tr {
      border-top: 1px solid #efeff0;

      @include sm-block {
        display: block;
      }

      &.order-table__inner {
        border-top: 0;
      }

      &:nth-child(-n+2) {
        border-top: 0;
      }
    }

    th {
      background-color: #f1f4fa;
      height: 48px;
      font-weight: 400;
      text-align: left;
      padding-right: 30px;

      @include sm-block {
        display: none;
      }

      &:first-child {
        padding-left: 28px;
      }
    }

    td {
      vertical-align: top;
      padding: 25px 30px 0 0;
      max-width: 154px;
      height: 123px;
      position: relative;

      @include sm-block {
        height: auto;
        max-width: none;
        padding: 0 30px 8px 20px;
        display: flex;
        align-items: center;
      }

      &:first-child {
        padding-top: 0;
        max-width: none;

        @include sm-block {
          padding: 0 0 12px 0;
        }
      }

      &:last-child {
        @include sm-block {
          padding: 0 30px 35px 20px;
        }

        .price {
          color: $accent;
        }
      }
    }
  }

  &__mobile-title {
    display: none;

    @include sm-block {
      display: block;
      width: 130px;
    }
  }

  &__color,
  &__counter {
    position: relative;
    top: -7px;

    @include sm-block {
      top: 0;
    }
  }

  &__color {
    @include sm-block {
      width: 18px;
      height: 18px;
    }
  }

  &__counter {
    @include sm-block {
      height: 20px;
      width: 100px;
    }

    .counter__btn {
      position: relative;
      cursor: pointer;

      &:before {
        content: '';
        display: block;
        position: absolute;
        top: -10px;
        bottom: -10px;
        left: -10px;
        right: -10px;
      }
    }
  }

  &__counter-desc {
    color: #727273;
    font-size: 12px;
    line-height: 16px;
  }

  &__del {
    position: absolute;
    right: 10px;
    bottom: 10px;

    @include sm-block {
      right: 0;
    }
  }
}
