.sale-info {
  display: flex;
  flex-wrap: wrap;
  margin-top: 30px;
  position: relative;

  &:before {
    content: '%';
    position: absolute;
    font-size: 40px;
    font-weight: 600;
    color: $accent;
    top: -80px;
    left: -20px;

    @include sm-block {
      left: 0;
      top: -70px;
    }
  }

  &__left {
    width: 50%;
    padding: 0 30px 0 35px;

    @include sm-block {
      width: 100%;
      padding: 0;
      margin-bottom: 30px;
    }
  }

  &__right {
    width: 50%;

    @include sm-block {
      width: 100%;
    }
  }

  &__title {
    font-size: 18px;
    text-transform: uppercase;
    margin-bottom: 20px;
  }

  &__desc {
    font-size: 16px;
    line-height: 21px;
    color: $accent;
  }

  &__table {
    max-width: 365px;
    width: 100%;

    th {
      font-weight: 500;
      text-align: left;
      padding-bottom: 20px;
    }

    td {
      padding: 0 10px 7px 0;

      &:last-child {
        font-weight: 600;
        color: $accent;
        text-align: right;
      }
    }
  }
}
