.main-slider {
  margin-bottom: 70px;

  @include md-block {
    margin-bottom: 50px;
  }

  &__main {
    background-color: #f2f4f6;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    height: 518px;
    max-width: 1366px;
    margin: 0 auto;

    @include md-block {
      height: 303px;
    }

    @include xs-block {
      height: 240px;
    }
  }

  &__img {
    width: 50%;

    @include sm-block {
      width: 100%;
    }
  }

  &__content {
    width: 50%;

    @include sm-block {
      display: none;
    }
  }

  &__thumbs {
    margin: -10px 0 0;

    @include xs-block {
      margin: 0;
    }
  }
}
