.form {
  &__row {
    display: flex;
    flex-wrap: wrap;
    margin-left: -15px;
    margin-right: -15px;

    & + & {
      margin-top: 15px;

      @include sm-block {
        margin-top: 5px;
      }
    }
  }

  &__col {
    padding-left: 15px;
    padding-right: 15px;
    width: 50%;

    @include xs-block {
      width: 100%;
    }

    &--12 {
      width: 100%;
    }
  }

  &__block {
    margin-bottom: 20px;

    &--text-right {
      text-align: right;
    }

    &--offset-top {
      margin-top: 20px;

      @include sm-block {
        margin-top: 0;
      }
    }
  }

  &__btn {
    width: 100%;
    max-width: 240px;

    &--xs-center {
      @include xs-block {
        //max-width: 280px;
        display: block;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }

  &__forget {
    line-height: 40px;
    color: #727273;
    font-size: 12px;
    border-bottom: 1px dotted currentColor;
    transition: all .3s ease;

    &:hover {
      color: $accent;
      border-color: transparent;
    }
  }

  &__state {
    &.hide {
      display: none;
    }
  }

  &__rules {
    color: #727273;
    font-size: 14px;
    margin-bottom: 30px;

    li {
      margin-bottom: 5px;
      position: relative;
      padding-left: 14px;

      &:before {
        content: '';
        display: block;
        height: 4px;
        width: 4px;
        border-radius: 50%;
        background-color: currentColor;
        position: absolute;
        top: 7px;
        left: 0;
      }

      &:last-child {
        margin-bottom: 0;
      }
    }
  }
}
