.element-main {
  margin-bottom: 65px;
  position: relative;

  @include r(1380px) {
    padding-right: 80px;
  }

  @include md-block {
    margin-bottom: 50px;
  }

  @include r($md-min) {
    padding-right: 0;
  }

  @include sm-block {
    margin-bottom: 40px;
  }

  .modal-fast & {
    margin-bottom: 50px;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
  }

  &__left {
    width: 54%;

    @include r($md-min) {
      width: 100%;
      max-width: 715px;
      margin-bottom: 40px;
      padding-right: 80px;
    }

    @include xxs-block {
      padding-right: 0;
    }
  }

  .modal-fast &__left {
    width: 100%;
    max-width: 715px;
    margin-bottom: 40px;
    padding-right: 0;

    @include xxs-block {
      padding-right: 0;
    }
  }

  &__right {
    width: 46%;
    padding-left: 65px;

    @include xl-block {
      padding-left: 30px;
    }

    @include r($md-min) {
      width: 100%;
      padding-left: 0;
    }
  }

  .modal-fast &__right {
    width: 100%;
    padding-left: 0;
  }

  &__title {
    display: block;
    font-size: 30px;
    font-weight: 500;
    line-height: 40px;
    margin: -9px 0 40px;

    @include sm-block {
      margin-bottom: 26px;
      margin: -20px 0 20px;
    }
  }

  .modal-fast &__title {
    @include sm-block {
      font-size: 20px;
      line-height: 30px;
    }
  }

  &__float-btn {
    position: absolute;
    top: 0;
    right: calc((-1 * (100vw - 100%) / 2) + 25px);
    z-index: 20;

    @include xxs-block {
      right: -6px;
      top: -20px;
    }
  }
}
