.input {
  width: 100%;
  height: 40px;
  border-radius: 5px;
  border: 1px solid #f0f0f1;
  background-color: #fff;
  padding: 0 10px;
  font-size: 15px;
  font-weight: 400;
  color: #1b2128;
  max-width: 240px;

  @include xs-block {
    max-width: none;
  }

  &::placeholder {
    opacity: .7;
  }

  &:focus {
    border-color: #e2e2e4;
  }

  &:disabled {
    opacity: .7;
  }

  &.error {
    border-color: $error;

    &::placeholder {
      color: $error;
    }
  }

  &--no-max-width {
    max-width: none;
  }

  &--textarea {
    padding-top: 10px;
    height: 40px;
    max-height: 107px;
  }

  &--textarea-height {
    min-height: 80px;
    max-height: none;
  }
}
