.element-sizes {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -5px;
  user-select: none;

  li {
    padding: 0 5px;
    margin-bottom: 10px;
  }

  &__checkbox {
    display: none;
  }

  &__label {
    display: block;
    width: 100px;
    line-height: 30px;
    border: 1px solid #f0f0f1;
    text-align: center;
    font-size: 14px;
    cursor: pointer;
    background-color: #fff;
    white-space: nowrap;
    overflow: hidden;
    padding: 0 3px;
  }

  &.error &__label {
    border: 1px solid $error;
  }

  &__checkbox:checked ~ &__label {
    background-color: #dde1e7;
    border: 1px solid #dde1e7;
  }
}
