.hint {
  position: relative;
  z-index: 5;

  &--ib {
    display: inline-block;
  }

  &__popup {
    padding: 12px 0;
    text-align: center;
    font-size: 13px;
    line-height: 1.5;
    font-weight: 500;
    display: block;
    position: absolute;
    top: calc(100% + 10px);
    left: 50%;
    width: 160px;
    transform: translate(-50%, 10px);
    background-color: #dde1e7;
    color: #28303b;
    z-index: 5;
    opacity: 0;
    visibility: hidden;
    transition: all .2s ease;
    transition-delay: .1s;

    body:not(.ready) & {
      display: none;
    }

    &:before {
      content: '';
      display: block;
      @include triangle(top, #dde1e7, 8px);
      position: absolute;
      bottom: 100%;
      left: 50%;
      transform: translate(-50%, 0);
    }

    &--top {
      top: auto;
      bottom: calc(100% + 10px);

      &:before {
        @include triangle(bottom, #dde1e7, 8px);
        top: 100%;
        bottom: auto;
      }
    }

    a {
      color: inherit;

      &:hover {
        text-decoration: underline;
      }
    }
  }

  &:hover &__popup {
    opacity: 1;
    visibility: visible;
    transform: translate(-50%, 0);
    transition-delay: 0s;
  }
}
