.float-btn-2 {
  &__item {
    margin-bottom: 20px;
    width: 50px;
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
    cursor: pointer;

    @include xxs-block {
      margin-bottom: 7px;
    }

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__icon {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
    margin-bottom: 5px;
    transition: background .3s ease;

    @include xxs-block {
      width: 38px;
      height: 38px;
    }

    .icon-sizes {
      font-size: 22px;

      @include xxs-block {
        font-size: 18px;
      }
    }

    .icon-sales {
      font-size: 32px;

      @include xxs-block {
        font-size: 24px;
      }
    }
  }

  &__item--size &__icon {
    background-color: $accent;
  }

  &__item--size:hover &__icon {
    background-color: $accentHover;
  }

  &__item--sale &__icon {
    background-color: $accent2;
  }

  &__item--sale:hover &__icon {
    background-color: darken($accent2, 10%);
  }

  &__title {
    font-size: 12px;
    line-height: 16px;
    font-weight: 400;

    @include xxs-block {
      display: none;
    }
  }
}
