.btn-del {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: $accent;
  font-size: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  transition: all .3s ease;

  &:hover {
    background-color: $accent;
    color: #fff;
  }

  .icon {
    font-size: 26px;
  }
}
