.user-nav {
  border: 1px solid $grey2;

  @include lg-block{
    display: flex;
  }

  @include sm-block {
    display: block;
  }

  &__header {
    padding: 30px;

    @include lg-block {
      width: 33.33%;
    }

    @include sm-block {
      width: 100%;
    }
  }

  &__body {
    @include lg-block {
      width: 33.33%;
      border: 1px solid $grey2;
      border-width: 0 1px;
    }

    @include sm-block {
      width: 100%;
      border: none;
    }
  }

  &__greeting {
    font-size: 16px;
    margin-bottom: 10px;
  }

  &__name {
    font-size: 24px;
    color: $accent;
  }

  &__list {
    li {
      border-bottom: 1px solid $grey2;

      &:first-child {
        border-top: 1px solid $grey2;

        @include lg-block {
          border-top: none;
        }

        @include sm-block {
          border-top: 1px solid $grey2;
        }
      }

      &:last-child {
        @include lg-block {
          border-bottom: none;
        }

        @include sm-block {
          border-bottom: 1px solid $grey2;
        }
      }
    }

    a {
      display: flex;
      align-items: center;
      height: 50px;
      padding: 0 20px;
      color: #1b2128;
      background-color: #fff;
      transition: all .3s ease;

      &:hover,
      &.active {
        background-color: $accent;
        color: #fff;

        .user-nav__counter {
          background-color: #fff;
          color: $accent;
        }
      }
    }
  }

  &__counter {
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    line-height: 20px;
    text-align: center;
    margin-right: 20px;
    background-color: $accent;
    color: #fff;
    font-size: 14px;
    transition: all .3s ease;
  }

  &__footer {
    background-color: #f1f4fa;
    padding: 30px;
    position: relative;

    @include lg-block {
      width: 33.33%;
    }

    @include sm-block {
      width: 100%;
    }

    &:after {
      content: '';
      display: block;
      height: 12px;
      width: 100%;
      position: absolute;
      z-index: 5;
      bottom: 0;
      left: 0;
      background-image: url('../img/pattern-green.png');
      background-position: 50%;
      opacity: .3;
    }

    .link-bold {
      display: inline-block;
      margin-top: 10px;
    }
  }
}
