.min-card {
  display: flex;

  &__img {
    position: relative;
    width: 135px;
    height: 123px;
    margin-right: 30px;
    flex-shrink: 0;

    @include sm-block {
      margin-right: 15px;
    }

    img {
      @include coverdiv();
      @include ofi(cover, center);
    }
  }

  &__content {
    padding-top: 25px;

    @include sm-block {
      padding-top: 10px;
    }
  }

  &__art {
    margin-bottom: 10px;
  }
}
