.form-table {
  border: 1px solid #e9e9eb;

  &__title {
    margin-bottom: 20px;
  }

  &__desc {
    font-size: 14px;
    margin-bottom: 30px;
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #e9e9eb;

    @include sm-block {
      display: block;
      padding: 30px 20px 10px;
    }

    &:last-child {
      border-bottom: none;
    }
  }

  &__row-2 {
    display: flex;
    flex-wrap: wrap;
    border-bottom: 1px solid #e9e9eb;

    @include sm-block {
      display: block;
      padding: 30px 20px;
    }

    &:last-child {
      border-bottom: none;
    }
  }

  &__left,
  &__right {
    padding: 30px 30px 10px;

    @include sm-block {
      padding: 0;
    }
  }

  &__left-2,
  &__right-2 {
    padding: 30px;

    @include sm-block {
      padding: 0;
    }
  }

  &__left {
    width: 30%;

    @include sm-block {
      width: 100%;
    }
  }

  &__right {
    width: 70%;

    @include sm-block {
      width: 100%;
    }
  }

  &__left-2 {
    width: 54%;

    @include sm-block {
      width: 100%;
    }
  }

  &__right-2 {
    width: 46%;

    @include sm-block {
      width: 100%;
    }
  }

  &__link {
    display: inline-block;
    margin-top: 10px;
  }

  &__line {
    height: 1px;
    background-color: #e9e9ea;
    margin: 5px 0 30px;

    @include sm-block {
      margin: 5px -20px 30px;
    }
  }

  &__block {
    flex-grow: 1;
    padding: 30px;

    @include sm-block {
      padding: 0 0 15px;
    }
  }
}
