.text-content ul:not([class]) {
  list-style: none;
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }

  li {
    position: relative;
    padding-left: 12px;
    margin-bottom: .5em;

    &:last-child {
      margin-bottom: 0;
    }

    &:before {
      content: '';
      display: block;
      height: 5px;
      width: 5px;
      border-radius: 50%;
      background-color: $accent;
      position: absolute;
      top: 0.5em;
      left: 0;
    }
  }
}

.text-content ol:not([class]) {
  list-style: none;
  margin-bottom: 20px;
  counter-reset: list;

  &:last-child {
    margin-bottom: 0;
  }

  li {
    position: relative;
    margin-bottom: 1em;

    @include sm-block {
      padding-left: 45px;
    }

    &:last-child {
      margin-bottom: 0;
    }

    &:before {
      counter-increment: list;
      content: counter(list);
      height: 25px;
      width: 25px;
      border-radius: 50%;
      background-color: $accent;
      position: absolute;
      display: flex;
      align-items: center;
      justify-content: center;
      color: #fff;
      top: -0.1em;
      left: -45px;

      @include sm-block {
        left: 0;
        top: -0.2em;
      }
    }
  }
}

.wide-slider {
  margin-left: -100px;
  margin-right: -100px;
  position: relative;

  @include lg-block {
    margin-left: -51px;
    margin-right: -51px;
  }

  @include md-block {
    margin-left: -21px;
    margin-right: -21px;
  }

  .slick-list {
    padding-bottom: 50px;
  }

  &:not(.slick-initialized) {
    display: flex;
    width: calc(100% + 200px);
    overflow: hidden;

    @include lg-block {
      width: calc(100% + 102px);
    }

    @include lg-block {
      width: calc(100% + 42px);
    }
  }

  .slick-arrow {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    z-index: 5;
    border: none;
    cursor: pointer;
    background-color: #dde1e7;
    top: calc(50% - 30px);
    transform: translateY(-50%);
    font-size: 0;
    opacity: .8;
    transition: opacity .3s ease;

    &:hover {
      opacity: 1;
    }

    .icon {
      font-size: 16px;
    }
  }

  .slick-prev {
    left: 35px;

    @include r(1366px) {
      left: 50px;
    }

    @include lg-block {
      left: 20px;
    }

    .icon {
      left: -1px;
    }
  }

  .slick-next {
    right: 35px;

    @include r(1366px) {
      right: 50px;
    }

    @include lg-block {
      right: 20px;
    }

    .icon {
      right: -1px;
    }
  }

  &__item {
    padding: 0 1px;
    height: 320px;

    @include xs-block {
      height: 200px;
    }
  }

  &__wrap {
    display: block;
    position: relative;
    height: 100%;
    color: inherit;

    &:after {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba($accent, .3);
      transform: translate3d(0, 0, 0);
      transition: opacity .3s ease;
    }

    &:hover:after {
      opacity: 0;
    }

    &--material {
      &:after {
        display: none;
      }
    }
  }

  &__img {
    height: 100%;
    width: auto;
    max-width: none;
    display: block;
  }

  &__title {
    font-size: 30px;
    line-height: 40px;
    color: #fff;
    position: absolute;
    bottom: 30px;
    left: 20px;
    z-index: 2;

    @include sm-block {
      font-size: 16px;
      line-height: 21px;
      bottom: 15px;
    }
  }

  &__title-2 {
    position: absolute;
    top: calc(100% + 10px);

    @include sm-block {
      padding: 0 20px;
    }
  }
}

