.history-slider {
  position: relative;

  &:before {
    content: '';
    display: block;
    height: 4px;
    left: calc(-1 * (100vw - 100%) / 2);
    right: calc(-1 * (100vw - 100%) / 2);
    top: 10px;
    background-color: $accent;
    position: absolute;
  }

  .slick-list {
    overflow: visible;
  }

  &:not(.slick-initialized) {
    display: flex;
  }

  .slick-arrow {
    position: absolute;
    top: -3px;
    z-index: 5;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #dde1e7;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    opacity: 1;
    transition: opacity .3s ease;

    @include sm-block {
      display: none;
    }
  }

  .slick-disabled {
    opacity: 0;
    visibility: hidden;
  }

  .slick-prev {
    left: 0;

    .icon {
      left: -1px;
    }
  }

  .slick-next {
    right: 0;

    .icon {
      right: -1px;
    }
  }

  &__item {
    flex-shrink: 0;
  }
}

.history-item {
  width: 400px;
