.question-block {
  margin-bottom: 20px;

  &__title {
    min-height: 43px;
    padding: 8px 0 0 73px;
    position: relative;
    color: #28303b;
    font-size: 18px;
    line-height: 28px;
    font-weight: bold;
    cursor: pointer;
    user-select: none;
    transition: background-color .3s ease;
  }

  &__title-btn {
    height: 43px;
    width: 43px;
    background-color: $accent;
    position: absolute;
    top: 0;
    left: 0;

    &:before,
    &:after {
      content: '';
      display: block;
      height: 4px;
      width: 20px;
      background-color: #fff;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      transition: opacity .3s ease;
    }

    &:after {
      transform: translate(-50%, -50%) rotate(90deg);
    }
  }

  &.active &__title-btn,
  &__title:hover &__title-btn {
    background-color: $accent2;
  }

  &.active &__title-btn {
    &:after {
      opacity: 0;
    }
  }

  &__body {
    display: none;
    padding: 20px 0 0 73px;

    @include sm-block {
      padding: 20px 0 0 0;
    }
  }
}

