    }
  }

  .slick-arrow {
    background-color: #dde1e7;
    border: none;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 0;
    position: absolute;
    bottom: 0;
    opacity: .8;
    transition: opacity .3s ease;
    z-index: 5;

    &:hover {
      opacity: 1;
    }

