  &__item {
    width: p(1, 5);
    padding: 0 15px;
    margin-bottom: 50px;
  }

  &__link {
    display: block;
    text-align: center;
    text-decoration: none;
  }

  &__img {
    display: block;
    max-width: 200px;
    height: 90px;
    position: relative;
    opacity: .8;
    filter: grayscale(1);
    transition: all .3s ease;

    img {
      width: 100%;
      height: 100%;
      @include ofi(contain, center);
    }
  }

  &__link:hover &__img {
    filter: none;
    opacity: 1;
  }

  &__title {
    display: inline-block;
    margin-top: 10px;
    position: relative;
    color: $accent;

    &:after {
      content: '';
      display: block;
      height: 1px;
      width: 100%;
      background-color: currentColor;
      position: absolute;
      opacity: .5;
      bottom: 3px;
      left: 0;
      transition: opacity .3s ease;
    }
  }

  &__link:hover &__title {
    &:after {
      opacity: 0;
    }
  }
