    border-bottom: 1px dotted currentColor;
    transition: all .2s ease;

    &:hover {
      border-bottom: 1px dotted transparent;
    }
  }
}

.news-detail-page {
  margin-bottom: 70px;
  padding: 0 40px;

  @include md-block {
    padding: 0;
  }

  &__date {
    color: #727273;
    margin-bottom: 20px;

    @include sm-block {
      font-size: 12px;
      margin-bottom: 10px;
    }
  }

  &__img {
    float: left;
    max-width: 440px;
    width: 50%;
    margin: 0 60px 20px -40px;

    @include md-block {
      margin: 0 30px 20px 0;
    }

    @include sm-block {
      float: none;
      width: 100%;
    }

    img {
      display: block;
    }
  }
}

.clear {
  clear: both;
}

.reviews-page {
  margin-bottom: 70px;
}

.reviews-list {
  margin: 0 -25px;
  display: flex;
  flex-wrap: wrap;

  &__item {
    padding: 0 25px;
    width: 50%;
    margin-bottom: 50px;

    @include sm-block {
      width: 100%;
    }
  }
}
