.category-list {
  @include row();

  &__item {
    @include col();
    width: 20%;
    margin-bottom: 30px;

    @include lg-block {
      width: 25%;
    }

    @include md-block {
      width: 33.33%;
    }

    @include sm-block {
      width: 50%;
    }

    @include xs-block {
      width: 100%;
      margin-bottom: 20px;
    }
  }
}
