.reviews-item {
  display: flex;
  flex-wrap: wrap;

  a {
    color: inherit;
    transition: color .3s ease;

    &:hover {
      color: $accent;
    }
  }

  &--full {
    font-size: 14px;
  }

  &__left {
    width: 70px;
    flex-shrink: 0;
    margin-right: 30px;

    @include r(700px) {
      width: 30px;
      margin-right: 20px;
    }
  }

  &--full &__left {
    width: 135px;
    margin-right: 30px;

    @include md-block {
      width: 80px;
      margin-right: 20px;
    }

    @include xxs-block {
      width: 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }
  }

  &__right {
    width: calc(100% - 100px);

    @include r(700px) {
      width: calc(100% - 50px);
    }
  }

  &--full &__right {
    width: calc(100% - 165px);

    @include md-block {
      width: calc(100% - 100px);
    }

    @include xxs-block {
      width: 100%;
    }
  }

  &__head {
    font-weight: 600;
    margin-bottom: 20px;
    padding-right: 80px;
  }

  &__art {
    margin-bottom: 5px;
  }

  &__title {
    margin-bottom: 5px;
  }

  &__name {
    color: $accent;
    margin-bottom: 5px;
  }

  &__date {
    color: #727273;
  }

  &__text {
    padding-right: 40px;
    font-size: 14px;
    line-height: 19px;
    will-change: height;
    transform: translate3d(0, 0, 0);
    @include crop(3, 19);

    @include lg-block  {
      padding-right: 0;
    }
  }

  &__text-detail {
    display: none;
  }

  &__readall {
    display: inline-block;
    margin-top: 10px;
    color: #727273;
    border-bottom: 1px dotted currentColor;
    float: right;
  }
}
