.main-slider-thumbs {
  position: relative;

  //margin-right: calc(-1 * 100vw - 100%);

  &__slider {
    margin-left: calc(50% - 590px);
    position: relative;
    display: inline-block;

    @include lg-block {
      margin-left: 20px;
    }

    @include xs-block {
      margin-left: 0;
    }

    &:not(.slick-initialized) {
      display: inline-flex;
    }

    & .slick-list,
    & .slick-track,
    & .slick-slide > div {
      height: 100%;
    }

    .slick-list {
      overflow: visible;
    }

    .slick-track {

    }

    .slick-slide {
      width: 295px;
      cursor: pointer;
      //position: relative;
      box-shadow: 0px 3px 20px 0px rgba(27, 34, 43, 0.07);

      & + .slick-slide {
        border-left: 1px solid #e9edf4;
      }
    }
  }

  &__arrow {
    position: absolute;
    z-index: 10;
    cursor: pointer;
    top: 20px;
    font-size: 0;
    width: 30px;
    height: 30px;
    border-radius: 50%;
    border: none;
    transition: all .2s ease;

    @include sm-block {
      top: -15px;
    }

    &:not(.slick-arrow),
    &.slick-disabled {
      opacity: 0;
      visibility: hidden;
    }

    .icon {
      font-size: 12px;
    }

    .icon-chevron-left {
      left: -1px;
    }

    .icon-chevron-right {
      right: -1px;
    }

    &--prev {
      left: 50%;
      margin-left: -605px;

      @include lg-block {
        left: 5px;
        margin-left: 0;
      }

      @include sm-block {
        left: 20px;
      }
    }

    &--next {
      right: 50%;
      margin-right: -605px;

      @include lg-block {
        right: auto;
        left: 890px;
        margin-right: 0;
      }

      @include md-block {
        left: 595px;
      }

      @include sm-block {
        left: auto;
        right: 20px;
      }
    }
  }

  &__item {
    background-color: #fff;
    width: 295px;
    height: 134px;
    padding: 35px 40px 20px;
    cursor: pointer;
    position: relative;
    flex-shrink: 0;
    transition: background-color .3s ease;

    @include xs-block {
      height: 130px;
      padding: 30px 20px 20px;
    }

    & + & {
      border-left: 1px solid #e9edf4;
    }

    &:after {
      content: '';
      display: block;
      height: 10px;
      width: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-image: url('../img/pattern-green.png');
      opacity: 0;
      transition: opacity .2s ease;

      @include sm-block {
        display: none;
      }
    }

    &:hover {
      background-color: #f2f4f6;
    }
  }

  .slick-slide.active &__item {
    &:after {
      opacity: 1;
    }
  }

  &__title {
    font-size: 16px;
    line-height: 21px;
    margin-bottom: 10px;
  }

  &__link {
    color: #7e8389;
    font-size: 13px;
    font-weight: 300;
    position: relative;

    .icon {
      font-size: 8px;
      margin-left: 5px;
    }
  }
}
