.catalog-item {
  position: relative;
  z-index: 0;

  &:after {
    content: '';
    position: absolute;
    top: -10px;
    left: -10px;
    right: -10px;
    bottom: -10px;
    z-index: -1;
    background-image: url('../img/pattern-green.png');
    background-repeat: repeat;
    transform: scale(.9);
    opacity: 0;
    transition: all .3s ease;

    @include sm-block {
      display: none;
    }
  }

  &:hover {
    &:after {
      transform: scale(1);
      opacity: 1;
    }
  }

  &__img {
    position: relative;
    overflow: hidden;
    @include ratio(270, 246);

    img {
      @include coverdiv();
      @include ofi(cover, top center);
    }
  }

  &__label {
    width: 100px;
    height: 100px;
    position: absolute;
    top: 0;
    left: 0;
    transform-origin: center;
    transform: translate(-50%, -50%) rotate(-45deg);
    z-index: 2;
    font-size: 13px;
    background-color: $accent;
    color: #fff;
    display: flex;
    align-items: flex-end;
    justify-content: center;
    padding-bottom: 5px;

    &--discont {
      font-size: 16px;
      background-color: $accent2;
    }
  }

  &__btn {
    height: auto;
    line-height: 1.2;
    font-weight: 500;
    padding: 7px 15px;
    font-size: 12px;
  }

  &__category {
    position: absolute;
    bottom: 20px;
    left: 30px;
    background-color: #fff;
    color: #28303b;
    transition: all .2s ease;

    &:not(:disabled):hover {
      background-color: #727273;
      color: #fff;
    }
  }

  &:hover &__category {
    opacity: 0;
    transform: translateY(-100%);
  }

  &__content {
    background-color: #f6f7f8;
    padding: 20px 30px 25px;
    font-size: 14px;
  }

  &__art {
    font-weight: bold;
    height: 20px;
    overflow: hidden;
  }

  &__sale {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20px;
  }

  &__price {
    font-size: 24px;
  }

  &__fast {
    position: absolute;
    bottom: 20px;
    left: 30px;
    right: 30px;
    background-color: #727273;
    color: #fff;
    opacity: 0;
    transform: translateY(calc(100% + 5px));
    transition: all .2s ease;
  }

  &:hover &__fast {
    transform: translateY(0);
    opacity: 1;
    //transition-delay: .1s;
  }

  &__sizes {
    position: absolute;
    bottom: 60px;
    left: 25px;
    right: 25px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    height: 155px;
    flex-wrap: wrap;
    opacity: 0;
    transition: all .3s ease;

    li {
      width: 50%;
      padding: 0 5px;
      margin-bottom: 10px;
    }
  }

  &:hover &__sizes {
      opacity: 1;
  }

  &__size {
    width: 100%;
    background-color: #fff;
    color: #727273;

    &:not(:disabled):hover {
      background-color: #727273;
      color: #fff;
    }
  }

  &__name {
    height: 38px;
    overflow: hidden;
  }
}
