.input-line-row {
  display: flex;
  flex-wrap: wrap;

  &__left {
    width: 185px;
    margin-right: 30px;

    @include sm-block {
      width: 100%;
      margin-bottom: 15px;
    }
  }

  &__right {
    flex-grow: 1;

    @include sm-block {
      width: 100%;
    }
  }
}
