.cart-layout {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 80px;

  &__left {
    width: calc(100% - 300px);
    margin-right: 30px;

    @include lg-block {
      width: 100%;
      margin-right: 0;
      order: 2;
    }
  }

  &__right {
    width: 270px;

    @include lg-block {
      width: 100%;
      margin-bottom: 40px;
    }
  }

  &__title {
    text-align: center;
    font-weight: 500;
    font-size: 24px;
    padding: 40px 0 30px;

    @include lg-block {
      padding: 50px 0 40px;
    }

    @include sm-block {
      padding: 30px 0 25px;
    }
  }

  &__checkout {
    margin-top: 40px;
    text-align: right;
    padding-right: 30px;

    @include sm-block {
      margin-top: 30px;
      text-align: center;
      padding-right: 0;
    }
  }

  &__btn {
    width: 240px;
    //margin-right: 30px;

    //@include sm-block {
    //  margin-right: 0;
    //}
  }
}
