.edit-btn {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  color: $accent;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  border: none;
  font-size: 0;
  cursor: pointer;
  transition: all .3s ease;

  @include sm-block {
    height: 35px;
    width: 35px;
  }

  &.active,
  &:hover {
    color: #fff;
    background-color: $accent;
  }

  .icon {
    font-size: 21px;

    @include sm-block {
      font-size: 18px;
    }
  }
}
