.section-title {
  text-align: center;
  font-size: 40px;
  line-height: 52px;
  margin-bottom: 45px;

  @include md-block {
    font-size: 26px;
    line-height: 34px;
  }

  @include sm-block {
    margin-bottom: 30px;
  }

  &--features {
    margin-bottom: 70px;

    @include md-block {
      margin-bottom: 50px;
    }
  }

  .icon {
    font-size: 44px;
    color: $accent;
    margin-right: 45px;
    top: 5px;

    @include md-block {
      margin-right: 20px;
    }

    @include sm-block {
      display: block;
      margin: 0 auto 20px;
    }
  }
}
