.user-main {
  border: 1px solid $grey2;

  &__row {
    padding: 30px 45px 5px 85px;
    border-bottom: 1px solid $grey2;
    position: relative;

    @include lg-block {
      padding: 30px 45px 5px 80px;
    }

    @include lg-block {
      padding: 30px 40px 5px 80px;
    }

    @include sm-block {
      padding: 30px 20px 5px;
    }

    &:last-child {
      border-bottom: 0;
      padding-bottom: 20px;
    }

    &--order {
      @include sm-block {
        padding: 50px 20px 5px 50px;
      }

      .user-main__icon {
        @include sm-block {
          position: absolute;
          left: 15px;
          top: 45px;
        }
      }
    }

    &--elements {
      padding: 0;

      @include lg-block {
        padding: 0;
      }

      @include lg-block {
        padding: 0;
      }

      @include sm-block {
        padding: 0;
      }
    }

    .form__row {
      max-width: 600px;
    }
  }

  &__icon {
    position: absolute;
    top: 25px;
    left: 30px;
    font-size: 30px;
    color: $accent;

    @include sm-block {
      position: static;
    }

    .icon-order-cart {
      width: 38px;
      height: 37px;
    }

    .icon-rubl-bold {
      font-size: 16px;
      top: -4px;
      left: 5px;
      color: $accent2;
    }

    .icon-arrow-right {
      color: $blue;
      font-size: 18px;
      top: -3px;
      left: 2px;
    }

    .icon-checked {
      font-size: 18px;
      top: -2px;
    }

    .icon-arrow-box {
      top: 1px;
      font-size: 28px;
    }
  }

  &__head {
    margin-bottom: 60px;

    &--order {
      margin-bottom: 30px;

      @include sm-block {
        margin-bottom: 20px;
      }
    }

    @include sm-block {
      margin-bottom: 20px;
    }
  }

  &__title {
    font-size: 24px;
    color: $accent;

    &--black {
      color: #1b2128;
    }

    &--orange {
      color: $accent2;
    }

    &--blue {
      color: $blue;
    }
  }

  &__desc {
    font-weight: normal;
    margin-top: 20px;
  }

  &__order-row {
    margin-bottom: 25px;
  }

  &__links {
    position: absolute;
    top: 40px;
    right: 45px;
    display: flex;

    @include sm-block {
      font-size: 13px;
      right: 20px;
      top: 15px;
    }
  }

  &__link {
    padding-right: 20px;

    @include sm-block {
      margin-right: 15px;
      padding-right: 0;
    }

    &:last-child {
      padding-right: 0;

      @include sm-block {
        margin-right: 0;
        padding-right: 0;
      }
    }

    &--follow {
      width:  200px;

      @include md-block {
        width: 150px;
      }

      @include sm-block {
        width: auto;
      }
    }

    &--view {
      width: 125px;

      @include sm-block {
        width: auto;
      }
    }

    &--bill {
      width: 200px;

      @include md-block {
        width: 170px;
      }

      @include sm-block {
        width: auto;
      }
    }
  }

  &__order-data {
    margin-bottom: 30px;
  }

  &__address-item {
    position: relative;

    & + & {
      margin-top: 50px;
      padding-top: 50px;
      //border-top: 1px solid #e2e2e4;

      @include sm-block {
        margin-top: 30px;
        padding-top: 30px;
      }

      &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: -45px;
        height: 1px;
        background-color: #e2e2e4;

        @include lg-block {
          right: -40px;
        }

        @include sm-block {
          left: -20px;
          right: -20px;
        }
      }
    }
  }
}
