.table-sizes {
  display: flex;
  font-size: 14px;
  font-weight: 600;

  td {
    padding-bottom: 20px;
  }

  &__left {
    width: 210px;
    padding-right: 30px;

    @include sm-block {
      width: 150px;
    }
  }

  &__right {
    width: calc(100% - 210px);
    overflow-x: auto;

    @include sm-block {
      width: calc(100% - 150px);
    }
  }

  &__table-titles {
    text-transform: uppercase;
    width: 100%;

    @include sm-block {
      font-size: 12px;
    }
  }

  &__table-values {
    width: 100%;

    td {
      padding-right: 10px;
    }
  }
}
