.cart-popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: z-index(catalog);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-delay: .3s;

  @include sm-block {
    display: none;
  }

  body:not(.ready) & {
    display: none;
  }

  &.active {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    cursor: pointer;
    transition-delay: 0s;
  }

  &__inner {
    cursor: auto;
    position: absolute;
    box-shadow: 0px 3px 40px 0px rgba(0, 0, 0, 0.05);
    top: 0;
    right: 0;
    width: 410px;
    height: 100%;
    background-color: #ffffff;
    transform: translateX(100%);
    transition: transform .3s ease;
  }

  &.active &__inner {
    transform: translateX(0);
  }

  &__close {
    position: absolute;
    top: 10px;
    left: 10px;
    font-size: 0;
    color: #8c929a;
    cursor: pointer;
    transition: color .3s ease;

    &:hover {
      color: $accent;
    }

    .icon {
      font-size: 17px;
    }
  }

  &__list {
    margin: 40px 0 20px;
    overflow-y: auto;
    height: calc(100% - 160px);

    .baron__scroller {
      padding: 0 30px 0 40px;
    }
  }

  &__item {
    margin-bottom: 50px;

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__bottom {
    height: 100px;
    border-top: 1px solid #f1f1f1;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 15px 10px 40px;
  }

  &__price-title {
    font-size: 16px;
  }

  &__price {
    color: $accent;
    font-size: 24px;
  }
}
