.paginator {
  display: flex;
  align-items: center;

  li {
    margin: 0 6px;

    &.paginator__first {
      margin-right: 20px;
    }

    &.paginator__last {
      margin-left: 20px;
    }
  }

  a {
    color: #727273;
    transition: color .3s ease;

    &:hover {
      color: #1b2128;
    }

    &.paginator__arrow {
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #eeeeef;
      color: #bababa;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      transition: background-color .3s ease;

      &:hover {
        background-color: darken(#eeeeef, 5%);
      }

      .icon-chevron-left {
        left: -1px;
      }

      .icon-chevron-right {
        right: -1px;
      }
    }
  }
}
