.tip {
  position: relative;

  &__title {
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: $accent;
    color: rgba(#fff, .9);
    text-align: center;
    line-height: 20px;
    font-size: 12px;
    cursor: pointer;
    user-select: none;
  }

  &__popup {
    position: absolute;
    left: -15px;
    bottom: 13px;
    width: 410px;
    background-color: #fff;
    border: 1px solid #dbdbdb;
    padding: 10px 25px 10px 15px;
    font-size: 14px;
    line-height: 21px;
    text-transform: none;
    font-weight: 500;
    opacity: 0;
    visibility: hidden;
    transition: all .3s ease-in-out;

    @include lg-block {
      width: 300px;
    }

    @include sm-block {
      left: -45px;
      width: 280px;
    }
  }

  &.active &__popup {
    opacity: 1;
    visibility: visible;
  }

  &__close {
    position: absolute;
    cursor: pointer;
    top: 10px;
    right: 10px;
    font-size: 0;

    &:before {
      content: '';
      display: block;
      position: absolute;
      top: -10px;
      left: -10px;
      bottom: -10px;
      right: -10px;
    }

    .icon {
      font-size: 11px;
    }
  }
}
