.user-order {
  display: flex;
  align-items: center;

  @include md-block {
    flex-wrap: wrap;
  }

  &--detail {
    align-items: flex-start;
    margin-top: 20px;

    @include md-block {
      flex-wrap: nowrap;
    }

    @include sm-block {
      flex-wrap: wrap;
    }
  }

  &__col {
    @include sm-block {
      padding-right: 0;
      margin-bottom: 15px;
      width: 100%;
    }

    &--desc {
      flex-grow: 1;
      padding-right: 20px;

      p {
        max-width: 320px;
      }
    }

    &--btn {
      flex-grow: 1;
      padding-right: 20px;

      @include md-block {
        order: 2;
        width: 100%;
        margin-top: 30px;
      }

      @include sm-block {
        margin: 15px 0 0;
      }
    }

    &--number {
      width: 200px;
      padding-right: 20px;

      @include md-block {
        width: auto;
        flex-grow: 1;
      }
    }

    &--number-2 {
      width: 200px;
      padding-right: 20px;

      @include md-block {
        //width: auto;
        //flex-grow: 1;
      }
    }

    &--date {
      width: 200px;
      padding-right: 20px;

      @include md-block {
        width: 170px;
      }
    }

    &--date-1 {
      width: 125px;

      @include md-block {
        width: 170px;
      }

      @include sm-block {
        //margin-bottom: 0;
      }
    }

    &--price {
      width: 125px;
    }
  }

  &__title {
    margin-bottom: 7px;

    @include sm-block {
      display: inline-block;
      margin-bottom: 0;
    }
  }

  &__value {
    font-weight: 600;

    @include sm-block {
      display: inline-block;
    }
  }
}
