.features-item {
  position: relative;
  padding-left: 105px;

  @include sm-block {
    padding-left: 0;
  }

  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 75px;
    height: 75px;
    font-size: 0;
    color: $accent;

    @include sm-block {
      position: relative;
      margin-bottom: 20px;
    }

    .icon {
      font-size: 75px;
    }

    .icon-features-2 {
      font-size: 70px;
    }

    .icon-features-3 {
      font-size: 57px;
    }

    .icon-features-4 {
      font-size: 58px;
    }

    .icon-features-5 {
      font-size: 47px;
    }

    .icon-features-6 {
      font-size: 70px;
    }
  }

  &__title {
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 20px;
  }

  &__desc {
    line-height: 24px;
  }
}
