.menu-catalog {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: z-index(catalog);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-delay: .3s;

  body:not(.ready) & {
    display: none;
  }

  @include sm-block {
    display: none;
  }

  &.active {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;
    cursor: pointer;
    transition-delay: 0s;
  }

  &__inner {
    cursor: auto;
    position: absolute;
    top: 0;
    left: 0;
    width: 270px;
    height: 100%;
    overflow-y: auto;
    background-color: $accent;
    color: #fff;
    padding-bottom: 30px;
    transform: translateX(-100%);
    transition: transform .3s ease;
  }

  &.active &__inner {
    transform: translateX(0);
  }

  &__title {
    padding: 40px 30px 30px;
    border-bottom: 1px solid #68908b;
    text-transform: uppercase;
    font-weight: 400;
    display: flex;
    align-items: center;
    margin-bottom: 40px;

    .icon {
      top: -3px;
      font-size: 44px;
      margin-right: 25px;
    }
  }

  &__list {
    li {
      margin-bottom: 20px;
    }
  }
}
