.input-select {
  position: relative;
  font-weight: 400;
  color: #1b2128;
  font-size: 15px;
  z-index: 1;
  user-select: none;
  transition-delay: .2s;
  max-width: 240px;

  @include xs-block {
    max-width: none;
  }

  &.active {
    z-index: 5;
    transition-delay: 0s;
  }

  &__title {
    width: 100%;
    height: 40px;
    line-height: 38px;
    border-radius: 5px;
    border: 1px solid #f0f0f1;
    background-color: #fff;
    padding: 0 10px;
    position: relative;
    z-index: 2;
    font-size: 0;
    cursor: pointer;

    .icon {
      position: absolute;
      font-size: 5px;
      top: 50%;
      right: 10px;
      margin-top: -2px;
      color: #8d929b;
      transition: transform .2s ease-in-out;
    }
  }

  &.readonly &__title {
    padding: 0;
    border: none;

    .icon {
      display: none;
    }
  }

  &.error &__title {
    border-color: $error;
    color: $error;
  }

  &--filter &__title {
    height: 30px;
    line-height: 28px;
    font-size: 16px;
    margin-bottom: 20px;
  }

  &.active &__title {
    .icon {
      transform: rotate(180deg);
    }
  }

  &__title-text {
    font-size: 15px;
  }

  .default &__title-text {
    opacity: .7;
  }

  &__list {
    position: absolute;
    z-index: 0;
    top: calc(100% - 5px);
    left: 0;
    width: 100%;
    border: 1px solid #f0f0f1;
    border-radius: 0 0 5px 5px;
    padding: 5px 0;
    background-color: #fff;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transform: translate3d(0, 0, 0);
    transition: all .2s ease-in-out;

    &--colors {
      display: flex;
      flex-wrap: wrap;
      align-items: flex-start;
      align-content: flex-start;
    }

    li {
      padding: 6px 12px;
      cursor: pointer;

      &:hover {
        background-color: #f0f0f1;
      }
    }
  }

  &--filter &__list {
    border: none;
    border-radius: 0;
    padding: 0;
    font-size: 14px;
    position: static;
    height: 0;
    overflow: hidden;

    li {
      padding: 5px 10px 5px 0;

      &:hover {
        background-color: transparent;
      }
    }
  }

  &--filter .baron {
    transition: margin-bottom .2s ease-in-out;
  }

  &--filter.active .baron {
    margin-bottom: 20px;
  }

  &--filter.active &__list {
    height: 130px;
    //padding: 15px 0 7px;
    overflow-y: auto;
  }

  &.active &__list {
    opacity: 1;
    visibility: visible;
    pointer-events: auto;

    &--no-scroll {
      overflow: visible;
    }
  }
}
