.element-options {
  font-size: 14px;

  &__flex {
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;

    @include lg-block {
      display: block;
    }

    @include r($md-min) {
      display: flex;
    }

    @include xxs-block {
      display: block;
    }
  }

  &__row {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 20px;
    position: relative;

    @include xxs-block {
      display: block;
      margin-bottom: 15px;
    }

    &--size {
      @include xxs-block {
        margin-bottom: 10px;
      }
    }

    &--count {
      width: 270px;
      margin-bottom: 75px;

      @include lg-block {
        margin-bottom: 75px;
      }

      @include r($md-min) {
        margin-right: 20px;
      }

      @include xxs-block {
        display: flex;
        margin-bottom: 60px;
      }

      .element-options__left {
        @include xxs-block {
          width: 120px;
        }
      }

      .element-options__right {
        @include xxs-block {
          width: calc(100% - 120px);
        }
      }
    }

    &--price {
      width: 200px;

      @include lg-block {
        margin-bottom: 10px;
        width: auto;
        display: flex;
      }

      @include r($md-min) {
        margin-right: auto;
      }

      @include xxs-block {
        display: flex;
      }

      .element-options__left {
        width: 60px;

        @include lg-block {
          width: 120px;
        }

        @include r($md-min) {
          width: 60px;
        }
      }

      .element-options__right {
        width: calc(100% - 60px);

        @include lg-block {
          width: auto;
        }
      }
    }
  }

  &__left {
    width: 120px;
    padding: 7px 20px 0 0;
    flex-shrink: 0;

    @include xxs-block {
      width: 100%;
      margin-bottom: 15px;
    }

    &--price {
      width: 60px;
    }
  }

  &__right {
    width: calc(100% - 120px);

    @include xxs-block {
      width: 100%;
    }
  }

  &__title {
    text-transform: uppercase;
    font-weight: 600;
  }

  &__tip {
    display: inline-block;
    margin-left: 5px;
    top: -1px;
  }

  &__total {
    display: flex;
  }

  &__count {
    margin-right: 50px;
  }

  &__price {
    display: flex;
  }

  &__price-title {
    padding-top: 7px;
    font-size: 14px;
    text-transform: uppercase;
    font-weight: 600;
    margin-right: 20px;
  }

  &__price-value {
    font-size: 30px;
    font-weight: 600;
    color: $accentHover;
    position: relative;
    top: -5px;
  }

  &__desc {
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    color: #727273;
    font-size: 12px;
    margin-top: 10px;

    @include xxs-block {
      margin-top: 0;
    }
  }

  &__buy {
    width: 100%;
    display: flex;
    align-items: center;
    //margin-top: 10px;

    @include r($md-min) {
      width: 210px;
      flex-wrap: wrap;
      margin-top: -5px;
      margin-right: 20px;
    }

    @include sm-block {
      margin-top: 0;
    }

    @include xxs-block {
      margin-top: -2px;
    }
  }

  &__min {
    color: #727273;
    font-size: 12px;
    line-height: 16px;
    max-width: 150px;
    margin-right: auto;

    @include r($md-min) {
      order: 1;
      margin-top: 7px;
    }
  }

  &__submit {
    width: 210px;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: .075em;

    .icon {
      font-size: 22px;
      margin-right: 30px;
      top: 5px;
    }
  }
}
