.reviews-slider {
  position: relative;

  &:not(.slick-initialized) &__item {
    display: none;

    &:first-child {
      display: block;
    }
  }

  .slick-arrow {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    background-color: #dde1e7;
    transition: all .3s ease;
    position: absolute;
    top: 0;
    border: none;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    z-index: 5;

    &:hover {
      background-color: darken(#dde1e7, 5%);
    }
  }

  .slick-prev {
    right: 40px;

    .icon {
      right: 1px;
    }
  }

  .slick-next {
    right: 0;

    .icon {
      left: 1px;
    }
  }
}
