.recall-btn {
  width: 52px;
  height: 52px;
  border-radius: 50%;
  position: fixed;
  z-index: 300;
  right: 100px;
  bottom: 100px;
  background-color: $accent;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 24px;
  transition: background-color .3s ease;

  @include xl-block {
    right: 50px;
    bottom: 60px;
  }

  @include md-block {
    right: 20px;
    bottom: 60px;
    width: 40px;
    height: 40px;
    font-size: 19px;
  }

  &:before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: inherit;
    opacity: .2;
    transform-origin: center;
    position: absolute;
    left: 0;
    top: 0;
    z-index: -1;
    animation: recall-btn 2.4s ease infinite;
  }

  &:hover {
    background-color: $accentHover;
  }
}

@keyframes recall-btn {
  0% {
    transform: scale(1);
    opacity: .5;
  }

  50% {
    transform: scale(1.5);
    opacity: 0;
  }

  100% {
    transform: scale(1);
    opacity: 0;
  }
}
