.user-layout {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 80px;

  @include sm-block {
    margin-bottom: 60px;
  }

  &__sidebar {
    width: 270px;
    margin-right: 30px;

    @include lg-block {
      width: 100%;
      margin: 0 0 40px 0;
    }
  }

  &__main {
    width: calc(100% - 300px);

    @include lg-block {
      width: 100%;
    }
  }

  &__main-btn {
    width: 240px;
    margin: 40px 0 0 85px;

    @include lg-block {
      margin: 40px 0 0 80px;
    }

    @include sm-block {
      display: block;
      margin: 40px auto 0;
    }
  }
}
