.catalog-slider {
  margin: 0 -15px;

  @include lg-block {
    max-width: 954px;
    margin: 0 auto;
  }

  @include md-block {
    max-width: 600px;
    //margin: 0 auto;
  }

  @include r(700px) {
    max-width: none;
    margin: 0 -20px;
  }

  .slick-list {
    padding: 10px 0;
  }

  .slick-track {
    margin-left: 0;
    margin-right: 0;
  }

  .slick-slide {
    @include r(700px) {
      width: 270px;
    }
  }

  &:not(.slick-initialized) {
    display: flex;

    @include r(700px){
      height: 370px;
      opacity: 0;
      overflow: hidden;
    }
  }

  &:not(.slick-initialized) &__item {
    width: 25%;
    display: none;

    @include lg-block {
      width: 33.33%;
    }

    @include md-block {
      width: 50%;
    }

    @include r(700px) {
      width: 100%;
    }

    &:nth-child(-n+4) {
      display: block;

      @include lg-block {
        display: none;
      }
    }

    &:nth-child(-n+3) {
      @include lg-block {
        display: block;
      }

      @include md-block {
        display: none;
      }
    }

    &:nth-child(-n+2) {
      @include md-block {
        display: block;
      }

      @include r(700px) {
        display: none;
      }
    }

    &:first-child {
      @include r(700px) {
        display: block;
      }
    }
  }

  .slick-arrow {
    width: 30px;
    height: 30px;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    margin-top: -15px;
    border: none;
    background-color: #dde1e7;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 10;
    cursor: pointer;
    transition: background .3s ease;

    &:hover {
      background-color: darken(#dde1e7, 5%);
    }

    .icon {
      font-size: 16px;
    }

    .icon-chevron-left {
      right: 1px;
    }

    .icon-chevron-right {
      left: 1px;
    }
  }

  .slick-prev {
    left: -50px;

    //@include xl-block {
    //  left: 0;
    //}
    //
    //@include md-block {
    //  left: -50px;
    //}
  }

  .slick-next {
    right: -50px;

    //@include xl-block {
    //  right: 0;
    //}
    //
    //@include md-block {
    //  right: -50px;
    //}
  }

  &__item {
    padding: 0 15px;

    @include sm-block {
      padding: 0 5px;
    }
  }
}
