.input-radio {
  &__input {
    display: none;
  }

  &__label {
    position: relative;
    display: block;
    padding-left: 40px;
    cursor: pointer;
    user-select: none;

    &:before {
      content: '';
      display: block;
      height: 20px;
      width: 20px;
      border: 1px solid $accent;
      border-radius: 50%;
      position: absolute;
      top: 0;
      left: 0;
      transition: background .3s ease;
    }
  }

  &__input:checked ~ &__label {
    &:before {
      background-color: $accent;
    }
  }

  &__title {
    display: block;
  }

  &__desc {
    display: block;
    margin-top: 10px;
    font-size: 14px;
    line-height: 21px;
    color: #727273;

    & + & {
      margin-top: 5px;
    }
  }
}
