.footer-menu {
  &__title {
    font-weight: 600;
    font-size: 18px;
    margin-bottom: 25px;
    position: relative;

    @include sm-block {
      margin-bottom: 0;
      padding: 10px 0;
    }

    &:before,
    &:after {
      content: '';
      display: none;
      width: 16px;
      height: 2px;
      background-color: #fff;
      position: absolute;
      top: 17px;
      right: 0;
      pointer-events: none;
      transform-origin: center;
      transition: transform .2s ease-in-out;

      @include sm-block {
        display: block;
      }
    }

    &:after {
      transform: rotate(90deg);
    }
  }

  &.active &__title {
    &:after {
      transform: scaleY(0);
    }
  }

  &__list {
    @include sm-block {
      display: none;
      padding: 12px 0;
    }

    &--columns {
      column-count: 2;

      @include sm-block {
        column-count: 1;
      }
    }

    li {
      position: relative;
      margin-bottom: 10px;

      &:last-child {
        margin-bottom: 0;
      }
    }

    a {
      color: inherit;
      opacity: .5;
      transition: opacity .2s ease;

      &:hover {
        opacity: 1;
      }
    }
  }

  &__list-icon {
    width: 20px;
    font-size: 0;
    position: absolute;
    top: 0;
    left: -38px;
    display: flex;
    justify-content: center;

    .icon {
      font-size: 16px;
    }

    .icon-phone {
      font-size: 16px;
    }

    .icon-mail {
      font-size: 12px;
      top: 2px;
    }

    .icon-map {
      font-size: 19px;
      top: -5px;
    }
  }

  &__text {
    opacity: .5;
  }
}
