.heading-1 {
  font-weight: bold;
}

.bold {
  font-weight: 600;
}

p {
  margin: 1em 0;

  &:first-child {
    margin-top: 0;
  }

  &:last-child {
    margin-bottom: 0;
  }
}

.justify {
  display: flex;
  justify-content: space-between;
}

.text-column {
  column-count: 2;
  column-gap: 30px;
  font-weight: 400;

  @include md-block {
    column-count: 1;
  }
}

h2 {
  margin: .7em 0;
  font-size: 30px;
  font-weight: 500;

  @include sm-block {
    font-size: 20px;
  }

  &:last-child {
    margin-bottom: 0;
  }

  &:first-child {
    margin-top: 0;
  }
}

.text-accent {
  font-weight: 700;
  color: $accent;
}

.text-danger {
  font-weight: 700;
  color: $error;
}
