.element-colors {
  display: flex;
  flex-wrap: wrap;
  margin: 0 -5px;

  li {
    padding: 0 5px;
    margin-bottom: 10px;
  }

  &__checkbox {
    display: none;
  }

  &__label {
    display: block;
    width: 30px;
    height: 30px;
    cursor: pointer;
    border: 1px solid #e8e8e8;

    &--title {
      width: 100px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      text-align: center;
      padding: 0 3px;
      white-space: nowrap;
      overflow: hidden;
    }
  }

  &.error &__label {
    border: 2px solid $error;
  }

  &__checkbox:checked ~ &__label {
    border: 2px solid $accent;
  }
}
