// colors
$textDark: #1b2128;
$textWhite: #fff;
//$accent: #01756f;
$accent: #42746e;
$accent2: #e7b34c;
$blue: #26358e;
$accentHover: #37615c;
$accent2Hover: darken(#e7b34c, 10%);
$grey: #222222;
$grey2: #ededee;
$linkColor: $accent;
$error: #9b0101;

$darkbBg: #28303b;

// fonts
$gilroy: 'Gilroy', sans-serif;
$font: $gilroy;

$grey-1: #727273;

// grid and responsive
$container: 1170px;
$columns: 12;
$offset: 30px;
$offset_one_side: ($offset / 2);
$fields: 50px; //если поля другой ширины

$break_xl: 1270px;
$break_lg: 1219px;
$break_md: 1023px;
$md-min: 940px;
$break_sm: 767px;
$break_xs: 549px;
$break_xxs: 449px;

$col-1-12: percentage(1/12);
$col-2-12: percentage(2/12);
$col-3-12: percentage(3/12);
$col-4-12: percentage(4/12);
$col-5-12: percentage(5/12);
$col-6-12: percentage(6/12);
$col-7-12: percentage(7/12);
$col-8-12: percentage(8/12);
$col-9-12: percentage(9/12);
$col-10-12: percentage(10/12);
$col-11-12: percentage(11/12);
$col-12-12: percentage(12/12);

$col-1-12-offset: #{calc(#{$col-1-12} - #{$offset})};
$col-2-12-offset: #{calc(#{$col-2-12} - #{$offset})};
$col-3-12-offset: #{calc(#{$col-3-12} - #{$offset})};
$col-4-12-offset: #{calc(#{$col-4-12} - #{$offset})};
$col-5-12-offset: #{calc(#{$col-5-12} - #{$offset})};
$col-6-12-offset: #{calc(#{$col-6-12} - #{$offset})};
$col-7-12-offset: #{calc(#{$col-7-12} - #{$offset})};
$col-8-12-offset: #{calc(#{$col-8-12} - #{$offset})};
$col-9-12-offset: #{calc(#{$col-9-12} - #{$offset})};
$col-10-12-offset: #{calc(#{$col-10-12} - #{$offset})};
$col-11-12-offset: #{calc(#{$col-11-12} - #{$offset})};
$col-12-12-offset: #{calc(#{$col-12-12} - #{$offset})};

$z-index: (
  bottom-menu: 310,
  header: 310,
  bottom-line: 300,
  left-line: 250,
  transition-cover: 240,
  modal-overlay-thanks: 235,
  modal-overlay: 230,
  overlay-bg: 200,
  header-hamb: 150,
  catalog: 130,
  mobile-menu: 100,
  float-btn: 100
);

