.breadcrumbs {
  margin: 30px 0 40px;
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;

  @include sm-block {
    margin: 20px 0 30px;
  }

  li {
    &:after {
      content: '/';
      display: inline-block;
      margin: 0 3px;
    }

    &:last-child {
      &:after {
        display: none;
      }
    }

    a {
      color: #1b2128;
    }
  }
}
