.menu-catalog-item {
  position: relative;
  padding: 0 40px;
  color: #fff;
  display: flex;
  align-items: center;
  opacity: .7;
  transform: translate3d(0, 0, 0);
  transition: opacity .3s ease;

  &:hover {
    opacity: 1;
  }

  &__icon {
    width: 30px;
    font-size: 0;
    text-align: center;
    margin-right: 45px;

    .icon {
      font-size: 30px;
    }
  }

  &__title {
    font-size: 16px;
    line-height: 21px;
  }
}
