.features-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @include lg-block {
    max-width: 690px;
    margin-left: auto;
    margin-right: auto;
  }

  @include sm-block {
    display: block;
  }

  &__item {
    width: 300px;
    margin-bottom: 90px;

    @include lg-block {
      margin-bottom: 60px;
    }

    @include sm-block {
      margin-bottom: 40px;
      width: 100%;
    }
  }
}
