.catalog-page {
  &__list {
    margin-bottom: 60px;

    @include sm-block {
      margin-bottom: 40px;
    }
  }

  &__text {
    margin-bottom: 70px;

    @include md-block {
      max-width: 600px;
      margin-left: auto;
      margin-right: auto;
      padding: 0 15px;
    }

    @include sm-block {
      padding: 0;
      margin-bottom: 50px;
    }
  }

  &__more-box {
    @include lg-block {
      text-align: left;
      padding: 0 15px;
    }

    @include sm-block {
      text-align: center;
      margin-bottom: 25px;
    }
  }
}
