.checkbox-block  {
  &--color {
    width: 30px;
    height: 30px;
  }

  &__input {
    display: none;
  }

  &__label {
    display: block;
    padding-left: 34px;
    position: relative;
    cursor: pointer;
    user-select: none;

    @include sm-block {
      padding-left: 20px;
    }

    .justify {
      div + div {
        margin-left: 10px;
      }
    }
  }

  &--min &__label {
    font-size: 12px;
    line-height: 16px;
    color: #727273;
  }

  &--color &__label {
    width: 100%;
    height: 100%;
    padding: 0;

    &[style="background: #ffffff"] {
      border: 1px solid #e2e2e4;
    }
  }

  &__icon {
    position: absolute;
    top: 0;
    left: 0;
    width: 15px;
    height: 15px;
    border: 1px solid #f0f0f1;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 0;

    .icon {
      font-size: 6px;
      opacity: 0;
      transition: opacity .2s ease;
    }
  }

  &__input:checked ~ &__label &__icon {
    border-color: $accent;

    .icon {
      opacity: 1;
    }
  }

  &--color &__input:checked ~ &__label {
    border: 2px solid $accent;
  }
}
