.main-slider-content {
  height: 100%;
  color: #1b2128;

  & .slick-list,
  & .slick-track,
  & .slick-slide > div {
    height: 100%;
  }

  &:not(.slick-initialized) &__item {
    display: none;

    &:first-child {
      display: inline-flex;
    }
  }

  &.slick-initialized &__item {
    display: inline-flex !important; /* slick добавляет inline-block */
  }

  &__item {
    display: inline-flex;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    padding: 20px 80px;

    @include lg-block {
      padding: 20px 80px 20px 40px;
    }

    @include md-block {
      padding: 20px 70px 20px 30px;
    }
  }

  &__container {
    max-width: 464px;
    font-weight: 500;
  }

  &__title {
    font-size: 40px;
    line-height: 52px;
    font-weight: 500;
    margin-bottom: 25px;

    @include md-block {
      font-size: 26px;
      line-height: 34px;
      margin-bottom: 10px;
    }
  }

  &__desc {
    font-size: 16px;
    line-height: 30px;

    @include md-block {
      line-height: 24px;
    }

    a {
      position: relative;
      z-index: 1;
      color: inherit;

      &:after {
        content: '';
        display: block;
        height: 1.6em;
        position: absolute;
        top: 50%;
        margin-top: -.8em;
        left: -2px;
        right: -2px;
        border-radius: 3px;
        background-color: #fff;
        z-index: -1;
      }
    }
  }

  &__btn {
    margin-top: 50px;

    @include md-block {
      margin-top: 25px;
    }
  }
}
