.baron {
  $block-name: &; // #{$block-name}__element
  $scrollbar-width: 8px;
  $scrollbar-width-light: 3px;

  $track-color: #f1f4fa;
  $bar-color: #8c929a;
  $bar-color-hover: #8c929a;

  position: relative;
  overflow: hidden;

  &__scroller {
    overflow-y: scroll;
    box-sizing: border-box;
    margin: 0;
    border: 0;
    padding: 0;
    padding-right: $scrollbar-width;
    width: 100%;
    height: 100%;
    -webkit-overflow-scrolling: touch; // remove line to customize scrollbar in iOs

    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
  }

  &--light &__scroller {
    padding-right: $scrollbar-width-light;
  }

  &__track {
    opacity: 0;
    position: absolute;
    z-index: 3;
    top: 0;
    bottom: 0;
    right: 0;
    width: $scrollbar-width;
    background-color: $track-color;

    #{$block-name}--scrollbar & {
      opacity: 1;
    }
  }

  &--light &__track {
    width: $scrollbar-width-light;
  }

  &__free {
    position: absolute;
    top: 0;
    bottom: 0;
    right: 0;
  }

  &__bar {
    display: none;
    position: absolute;
    left: -$scrollbar-width;
    z-index: 1;
    width: $scrollbar-width;
    box-sizing: border-box;
    pointer-events: auto;
    background: $bar-color;
    opacity: 1;

    #{$block-name}--scrollbar & {
      display: block;
    }

    #{$block-name}--dragging &,
    &:hover {
      background-color: $bar-color-hover;
    }
  }

  &--light &__bar {
    width: $scrollbar-width-light;
    left: -$scrollbar-width-light;
  }

  &__control {
    display: none;
  }
}
