.element-slider {
  display: flex;
  overflow: hidden;
  position: relative;

  @include xxs-block {
    margin: 0 17px 0 -20px;
    overflow: visible;
  }

  &__main {
    width: 74%;
    position: relative;
    //overflow: hidden;

    &:not(.slick-initialized) {
      //padding-top: calc(100% - 30px);
      //position: relative;

      //&:before {
      //  content: '';
      //  display: block;
      //  width: 100%;
      //  padding-top: calc(100% - 30px);
      //}
    }

    &:not(.slick-initialized) .element-slider__main-item {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }

    @include xxs-block {
      width: 100%;
    }

    &:before {
      content: '';
      display: block;
      width: 100%;

      @include xxs-block {
        padding-top: 100%;
      }
    }

    .slick-list,
    .slick-track,
    .slick-slide > div {
      height: 100%;
    }

    .slick-list {
      @include coverdiv;

      @include xxs-block {
        overflow: visible;
      }
    }

    .slick-prev {
      display: none !important;
    }

    .slick-next {
      position: absolute;
      top: 50%;
      right: -14px;
      width: 30px;
      height: 30px;
      border-radius: 50%;
      background-color: $accent;
      color: #fff;
      border: none;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 0;

      .icon {
        font-size: 16px;
        left: 1px;
      }
    }
  }

  &__main-item {
    height: 100%;

    @include xxs-block {
      padding-right: 3px;
    }
  }

  &__main:not(.slick-initialized) &__main-item {
    display: none;

    &:first-child {
      display: block;
    }
  }

  &__thumbs-wrap {
    width: calc(26% - 30px);
    position: relative;
    overflow: hidden;
    //margin-left: 30px;
    margin: -15px 0 -15px 30px;

    &:before {
      content: '';
      display: block;
      padding-top: calc(300% + 90px);
    }

    @include sm-block {
      display: none;
    }
  }

  &__thumbs {
    @include coverdiv;
    //margin: -15px 0;

    //&:before {
    //  content: '';
    //  display: block;
    //  width: calc(100% - 2px);
    //  position: absolute;
    //  top: 15px;
    //  left: 1px;
    //  padding-top: 100%;
    //  border: 2px solid $accent;
    //  z-index: 2;
    //}

    &:not(.slick-initialized) {
      margin: -15px 0;
      height: 100%;

      &:before {
        top: 30px;
      }
    }

    .slick-list {
      height: 100%;
    }

    .slick-track {
      height: 100%;
    }

    .slick-slide {
      margin: 15px 0;
      font-size: 0;
    }

    .slick-center .element-slider__thumbs-item {
      outline: 2px solid $accent;
      outline-offset: -2px;
    }

    .slick-arrow {
      position: absolute;
      z-index: 5;
      left: 50%;
      transform: translateX(-50%);
      width: 30px;
      height: 30px;
      border-radius: 50%;
      font-size: 0;
      border: none;
      background-color: $accent;
      color: #fff;
      cursor: pointer;
      transition: background .3s ease;

      &:hover {
        background-color: $accentHover;
      }

      .icon {
        font-size: 9px;
      }

      .icon-chevron-top-2 {
        top: -1px;
      }

      .icon-chevron-bottom-2 {
        bottom: -1px;
      }
    }

    .slick-prev {
      top: 25px;
    }

    .slick-next {
      bottom: 25px;
    }
  }

  &__thumbs-item {
    cursor: pointer;
    position: relative;
    padding-top: 100%;
    z-index: 1;
    transition: outline-color .3s ease;

    .slick-current & {
      outline: 2px solid $accent;
      outline-offset: -2px;
    }

    //&.active {
    //  outline: 2px solid $accent;
    //  outline-offset: -2px;
    //}

    img {
      @include coverdiv();
      @include ofi(cover, center);
      z-index: -1;
      display: block;
    }
  }

  &__thumbs:not(.slick-initialized) &__thumbs-item {
    display: none;
    margin: 30px 0;

    &:nth-child(-n+3) {
      display: block;
    }
  }

  &__main-img {
    position: relative;
    height: 100%;

    @include xxs-block {
      @include ratio(280, 256);
    }

    img {
      @include coverdiv();
      @include ofi(cover, center);
      display: block;
    }
  }
}

.loading {
  position: relative;
  pointer-events: none;
  opacity: .5;

  &:after {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: -17px 0 0 -17px;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    border-top: 5px solid rgba(255, 255, 255, 0.2);
    border-right: 5px solid rgba(255, 255, 255, 0.2);
    border-bottom: 5px solid rgba(255, 255, 255, 0.2);
    border-left: 5px solid rgba(255, 255, 255, 1);
    -webkit-transform: translateZ(0);
    -ms-transform: translateZ(0);
    transform: translateZ(0);
    animation: circle-loader-spin 1s infinite linear;
  }
}

@keyframes circle-loader-spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
